.pagination {}

.pagination__list {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.pagination__item {
    display: flex;
    align-items: center;
    justify-content: center;

    margin: 0 5px;

    background-color: #F8FAFD;
    border-radius: 10px;

    transition: .2s linear;

    color: #545454;

    &.active {
        background-color: #3990F1;

        color: #fff;
    }

    @media(min-width:1024px) {
        &:hover:not(.active, .disabled) {
            background-color: darken(#F8FAFD, 10%);
        }
    }
}

.pagination__link {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 41px;
    width: 31px;

    font-weight: 600;
    font-size: 18px;
    line-height: 25px;

    color: inherit;

    &--btn {
        width: auto !important;
        padding: 0 10px;
    }

    @media(max-width:1919px) {
        font-size: 16px;
        line-height: 21px;
    }

    @media(max-width:1439px) {
        font-size: 14px;
        line-height: 19px;

        width: 28px;
        height: 37px;
    }
}