.btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 164px;
    height: 47px;

    border-radius: 10px;

    transition: .2s linear;

    font-weight: 600;
    font-size: 16px;
    line-height: 22px;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;

        width: 144px;
        height: 42px;
    }

    @media(max-width:1439px) {
        font-size: 13px;
        line-height: 14px;
    }

    @media(max-width:1023px) {
        width: 136px;
        height: 41px;
    }

    &--white {
        background: #FFFFFF;
        color: #3990F1;

        @media(min-width:1024px) {
            &:hover {
                background: #1A6DCA;
                color: #fff;
            }
        }
    }

    &--trans {
        border: 1px solid #FFFFFF;
        color: #fff;

        @media(min-width:1024px) {
            &:hover {
                background: #1A6DCA;
                color: #fff;
                border-color: #1A6DCA;
            }
        }
    }

    &--trans-blue {
        border: 1px solid #3990F1;
        color: #3990F1;

        span {
            transition: .2s linear;
            color: inherit;
        }

        @media(min-width:1024px) {
            &:hover {
                background: #1A6DCA;
                color: #fff;
                border-color: #1A6DCA;
            }
        }
    }

    &--blue {
        color: #FFFFFF;
        background: #3990F1;

        @media(min-width:1024px) {
            &:hover {
                background: #1A6DCA;
            }
        }
    }
}

.sign-in__btn {
    width: 113px;
    margin-right: 15px;

    @media(max-width:1023px) {
        width: 90px;
        color: #3990F1;
        border-color: #3990F1;
    }
}

.sign-up__btn {
    @media(max-width:1023px) {
        color: #FFFFFF;
        background-color: #3990F1;
    }
}