.private {
    padding-top: 105px;
    margin-bottom: 60px;

    position: relative;
    z-index: 2;

    @media(max-width:767px) {
        padding-top: 77px;
        margin-bottom: 30px;
    }
}

.private-bg {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    z-index: -1;
}

.private__board {
    background-color: #F8FAFD;
    box-shadow: 0px 100px 80px rgba(6, 34, 132, 0.0186011), 0px 36.5016px 29.2013px rgba(6, 34, 132, 0.0266226), 0px 17.7209px 14.1767px rgba(6, 34, 132, 0.0333774), 0px 8.6871px 6.94968px rgba(6, 34, 132, 0.0413989), 0px 3.43489px 2.74791px rgba(6, 34, 132, 0.06);
    border-radius: 10px;

    @media(max-width:1919px) {
        max-width: 1000px;
        margin: 0 auto;
    }
}

.private__nav {
    background-color: #EBF0F8;
    border-radius: 10px 10px 0px 0px;

    @media(max-width:1023px) {
        display: none;
    }
}

.private__list {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 20px 30px;
}

.private__item {
    &+& {
        margin-left: 40px;

        @media(max-width:1919px) {
            margin-left: 30px;
        }
    }

    &:last-child {
        margin-left: auto;
    }

    &--alert {
        padding-right: 33px;
        position: relative;

        &::before {
            content: url("../img/alert.svg");
            display: flex;
            align-items: center;
            justify-content: center;
            width: 22px;
            height: 22px;

            background: #3990F1;
            border-radius: 50%;

            position: absolute;
            top: 50%;
            right: 0;
            transform: translateY(-50%);
        }
    }
}

.private__btn {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #545454;

    cursor: pointer;
    display: block;
    overflow: hidden;

    position: relative;

    &::before {
        content: '';
        height: 1px;
        background-color: #545454;

        transform: translateX(-100%);
        transition: .2s linear;

        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
    }

    @media(min-width:1024px) {
        &:hover:not(.active)::before {
            transform: translateX(0);
        }
    }

    &.active {
        font-weight: 700;
        color: #78A1CF;
    }

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 20px;
    }
}

.private__log-off {
    display: flex;
    align-items: center;

    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    color: #545454;
    fill: #545454;

    transition: .2s linear;

    @media(min-width:1024px) {
        &:hover {
            color: #000;
            fill: #000;
        }
    }


    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 20px;
    }
}

.private__log-off-icon {
    width: 20px;
    height: 20px;
    margin-right: 7px;

    fill: inherit;
}

.private__content {
    padding: 30px;

    @media(max-width:1023px) {
        padding: 15px 10px 20px;
    }
}

.private__title {
    font-weight: 600;
    font-size: 22px;
    line-height: 30px;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 20px;
        line-height: 27px;
    }

    @media(max-width:1439px) {
        font-size: 18px;
        line-height: 22px;
    }

    &--control-panel {
        margin-bottom: 44px;

        @media(max-width:1919px) {
            margin-bottom: 30px;
        }

        @media(max-width:767px) {
            text-align: center;
        }
    }

    &--balance {
        margin-bottom: 20px;

        @media(max-width:1023px) {
            text-align: center;
        }
    }

    &--purchase-history {
        margin-bottom: 30px;

        @media(max-width:767px) {
            text-align: center;
            margin-bottom: 33px;
        }
    }

    &--settings {
        margin-bottom: 15px;

        @media(max-width:767px) {
            text-align: center;
        }
    }
}