.affiliate-program {
    display: flex;
    flex-wrap: wrap;
}

.referral-link {
    margin-bottom: 30px;
    margin-right: 30px;
}

.affiliate-program__title {
    font-weight: 600;
    font-size: 22px;
    line-height: 30px;
    color: #545454;

    margin-bottom: 30px;

    @media(max-width:1919px) {
        font-size: 18px;
        line-height: 26px;
    }

    @media(max-width:1439px) {
        font-size: 16px;
        line-height: 22px;
        margin-bottom: 15px;
    }

    &--referral {
        margin-bottom: 15px;
    }
}

.referral-link__text {
    font-weight: normal;
    font-size: 18px;
    line-height: 25px;
    color: #545454;

    margin-bottom: 15px;

    @media(max-width:1919px) {
        font-size: 16px;
        line-height: 21px;
    }

    @media(max-width:1439px) {
        font-size: 14px;
        line-height: 19px;
        margin-bottom: 10px;
    }
}

.referral-link__block {
    display: flex;
    align-items: center;
    margin-bottom: 26px;

    @media(max-width:1439px) {
        margin-bottom: 15px;
    }
}

.referral-link__input {
    width: 472px;
    height: 57px;
    padding: 16px 15px;
    margin-right: 10px;

    background: #EBF0F8;
    border: none;
    border-radius: 10px;

    font-weight: normal;
    font-size: 18px;
    line-height: 25px;
    color: #696969;

    @media(max-width:1919px) {
        font-size: 16px;
        line-height: 23px;
        height: 47px;
    }

    @media(max-width:1024px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 16px;
        width: 263px;
        height: 38px;
        margin-right: 7px;
    }
}

.referral-link__btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 58px;
    height: 57px;

    background: #EBF0F8;
    border-radius: 10px;

    img {
        width: 21px;
        height: 21px;

        transition: .2s linear;

        @media(max-width:767px) {
            width: 14px;
            height: 14px;
        }
    }

    @media(min-width:1024px) {
        &:hover img {
            transform: scale(1.1);
        }
    }

    @media(max-width:1919px) {
        height: 47px;
        width: 47px;
    }

    @media(max-width:767px) {
        width: 38px;
        height: 38px;
    }
}

.referral-link__descr {
    max-width: 540px;
    margin-bottom: 20px;

    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:1439px) {
        margin-bottom: 10px;
    }

    @media(max-width:1023px) {
        line-height: 16px;
    }
}

.referral-link__clarification {
    max-width: 540px;

    font-weight: normal;
    font-size: 14px;
    line-height: 19px;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 17px;
    }

    @media(max-width:767px) {
        line-height: 14px;
    }
}

.affiliate-program__stats {
    min-width: 157px;
    margin-bottom: 30px;

    @media(max-width:767px) {
        width: 308px;

        .affiliate-program__title {
            margin-bottom: 15px;
        }
    }
}


.affiliate-program__stats-item {
    display: flex;
    align-items: center;
    justify-content: space-between;

    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 19px;
    }

    span {
        font-weight: 600;
        font-size: 16px;
        line-height: 22px;
        color: #545454;

        @media(max-width:1919px) {
            font-size: 14px;
            line-height: 19px;
        }
    }

    &+& {
        margin-top: 15px;

        @media(max-width:1439px) {
            margin-top: 10px;
        }

        @media(max-width:767px) {
            margin-top: 8px;
        }
    }
}

.referral {
    width: 100%;
    padding: 15px 15px 20px 15px;

    background: #FFFFFF;
    border-radius: 10px;

    @media(max-width:767px) {
        padding: 0;
        background: transparent;
    }
}

.referral__nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 20px;
}

.referral__show,
.referral__search {
    display: flex;
    align-items: center;

    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 16px;
    }
}

.referral__search {
    @media(max-width:767px) {
        display: none;
    }
}

.referral__search-input {
    width: 174px;
    height: 32px;
    padding: 5px 10px;
    margin-left: 10px;

    background: #F8FAFD;
    border-radius: 10px;
    border: none;

    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
    }
}

.referral__board {
    background: #F8FAFD;
    border-radius: 10px;

    @media(max-width:767px) {
        background-color: #fff;
    }
}

.referral__board-top {
    display: flex;
    align-items: center;
    padding: 13px 17px 12px 33px;

    background: #EBF0F8;
    border-radius: 10px 10px 0px 0px;

    @media(max-width:767px) {
        padding: 8px 5px;
    }

    .referral__column {
        font-weight: 600;
        font-size: 16px;
        line-height: 22px;
        color: #545454;

        @media(max-width:1919px) {
            font-size: 14px;
            line-height: 18px;
        }

        @media(max-width:767px) {
            font-size: 13px;
            line-height: 16px;
        }
    }
}

.referral__column {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #545454;


    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:767px) {
        font-size: 11px;
        line-height: 14px;
    }

    span {
        font-weight: inherit;
        font-size: inherit;
        line-height: inherit;
        color: inherit;

        &:nth-child(1) {
            @media(max-width:767px) {
                display: none;
            }
        }

        &:nth-child(2) {
            display: none;

            @media(max-width:767px) {
                display: block;
            }
        }
    }

    &:nth-child(1) {
        width: 286px;

        @media(max-width:767px) {
            width: auto;
            min-width: 94px;
            margin-right: 10px;
        }
    }

    &:nth-child(2) {
        width: 286px;

        @media(max-width:767px) {
            width: 55px;
            margin-right: 10px;
        }
    }

    &:nth-child(3) {
        width: 230px;
        margin-left: auto;

        @media(max-width:767px) {
            width: auto;
            min-width: 60px;
        }
    }
}

.referral__row {
    display: flex;
    align-items: center;
    padding: 0 17px 0 33px;
    margin-top: 15px;

    @media(max-width:767px) {
        padding: 0 5px;
        margin-top: 10px;
    }

    &:last-child {
        padding-bottom: 15px;

        @media(max-width:767px) {
            padding-bottom: 10px;
        }
    }

}