.header {
    position: fixed;
    left: -2px;
    right: -2px;
    top: 0;
    z-index: 100;
    padding: 29px 0 15px;

    @media(max-width:1023px) {
        transition: .2s linear;
    }

    @media(max-width:767px) {
        padding: 25px 0 10px;
    }

    &::before {
        content: '';
        // background: linear-gradient(90deg, #920046 0%, #4E90D9 100%);
        background-image: url("../img/overlay.png");
        background-size: cover;
        background-position: top;
        background-repeat: no-repeat;
        transition: .2s linear;
        opacity: 0;

        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: -1;

        @media(max-width:767px) {
            background-image: url("../img/overlay-mob2.png");
        }

        @media(max-width:375px) {
            background-image: url("../img/overlay-mob.png");
        }
    }

    &.active {
        &::before {
            opacity: 1;
        }

        @media(max-width:1023px) {
            padding-top: 15px;
        }

        @media(max-width:767px) {
            padding-top: 10px;
        }
    }


    &.authorized {

        .sign-in__btn,
        .sign-up__btn {
            display: none;
        }

        .balance-header {
            display: flex;
        }

        .user {
            display: flex;
        }

        .user-mob {
            @media(max-width:1023px) {
                display: flex;
            }
        }

        .menu-title {
            @media(max-width:1023px) {
                display: none;
            }
        }

        .nav__list {
            @media(max-width:1023px) {
                order: 2;
                margin-bottom: 0;
            }
        }

        .nav__another {
            @media(max-width:1023px) {
                flex-direction: column;
                order: 1;
            }
        }
    }
}

.header__inner {
    display: flex;
    align-items: center;

    @media(max-width:1023px) {
        justify-content: space-between;
    }
}

.header__logo {
    width: 180.43px;
    height: 42px;

    img {
        height: auto;
    }

    @media(max-width:1919px) {
        width: 160.43px;
        height: 36px;
    }

    @media(max-width:1023px) {
        width: 117px;
        height: 27.31px;
    }
}