.nav {
    display: flex;
    flex-grow: 1;
    justify-content: space-around;
    align-items: center;
    margin-left: 58px;

    @media(max-width:1054px) {
        margin-left: 40px;
    }

    @media(max-width:1023px) {
        position: absolute;
        top: 0;
        right: 0;
        z-index: 2;

        flex-direction: column;
        padding: 0 16px 30px 16px;
        margin-left: 0;
        width: 100%;
        max-width: 320px;
        transform: translateX(100vw);
        transition: .3s linear;

        background: #F8FAFD;
        border-radius: 0px 0px 0px 10px;

        &.active {
            transform: translateX(0);
        }
    }
}

.substrate {
    display: none;

    @media(max-width:1023px) {
        display: block;
        width: 110vw;
        height: 100vh;

        transform: translateX(110vw);
        transition: .3s linear;
        background: rgba(0, 0, 0, 0.25);

        position: absolute;
        top: 0;
        right: 0;

        &.active {
            transform: translateX(0);
        }
    }
}

.nav__list {
    display: flex;
    justify-content: space-around;
    align-items: center;

    @media(max-width:1023px) {
        justify-content: center;
        flex-direction: column;
        margin-bottom: 40px;
    }
}

.nav__item+.nav__item {
    margin-left: 31px;

    @media(max-width:1054px) {
        margin-left: 20px;
    }

    @media(max-width:1023px) {
        margin-left: 0;
    }
}

.nav__item--exit {
    display: none;

    @media(max-width:1023px) {
        display: block;
    }
}

.nav__link {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #FFFFFF;

    transition: .2s linear;

    @media(max-width:1439px) {
        font-size: 14px;
        line-height: 31px;
    }

    @media(max-width:1023px) {
        height: 41px;
        display: inline-block;
        padding: 5px 0;
        
        font-size: 15px;
        color: #545454;
    }

    @media(min-width:1024px) {
        &:hover {
            opacity: .7;
        }
    }

    &.active {
        font-weight: 700;

        @media(min-width:1024px) {
            &:hover {
                opacity: 1;
            }
        }
    }
}

.exit-button {
    font-weight: normal;
    font-size: 15px;
    line-height: 31px;
    color: #FF1C74;

    padding-top: 5px;
}

.nav__another {
    display: flex;
    align-items: center;
    margin-left: auto;

    @media(max-width:1023px) {
        margin-left: unset;
    }
}

.balance-header {
    display: none;
    align-items: center;

    @media(max-width:1023px) {
        order: 2;
        margin-bottom: 23px;
    }
}

.balance-header__text {
    margin-right: 5px;

    font-weight: bold;
    font-size: 16px;
    line-height: 22px;
    color: #FFFFFF;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 19px;
    }

    @media(max-width:1023px) {
        color: #545454;
        font-size: 15px;
    }
}

.balance-header__sum {
    margin-right: 10px;

    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: #FFFFFF;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 19px;
    }

    @media(max-width:1023px) {
        color: #545454;
        font-size: 15px;
    }
}

.balance-header__add {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 33px;
    height: 33px;

    transition: .2s linear;

    border-radius: 50%;
    background-color: #fff;

    svg {
        width: 11px;
        height: 11px;
        fill: #3990F1;

        @media(max-width:1023px) {
            fill: #545454;
        }
    }

    @media(max-width:1919px) {
        width: 24px;
        height: 24px;
    }

    @media(max-width:1023px) {
        width: 26px;
        height: 26px;
        border: 1px solid #545454;
    }

    @media(min-width:1024px) {
        &:hover {
            transform: scale(1.1);
        }
    }
}

.user {
    display: none;
    align-items: center;
    margin-left: 33px;

    position: relative;

    @media(max-width:1023px) {
        margin-left: auto;
    }
}

.user__login {
    margin-right: 10px;

    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #FFFFFF;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 19px;
    }

    @media(max-width:1023px) {
        color: #545454;
        margin-right: 0;
        margin-left: 10px;
        font-size: 15px;
    }
}

.user__btn {
    transition: .2s linear;

    @media(min-width:1024px) {
        &:hover {
            transform: scale(1.1);
        }
    }

    svg {
        width: 33px;
        height: 33px;
        fill: white;
        pointer-events: none;

        @media(max-width:1919px) {
            width: 27px;
            height: 27px;
        }


        @media(max-width:1023px) {
            width: 28px;
            height: 28px;
        }
    }

    &.alert {
        position: relative;

        &::before {
            content: '';
            width: 18px;
            height: 18px;

            background-image: url('../img/alert-red.svg');
            background-size: contain;
            position: absolute;
            top: -4px;
            left: -6px;

            @media(max-width:1919px) {
                width: 13px;
                height: 13px;
                top: -2px;
                left: -3px;
            }
        }
    }
}

.user__menu {
    display: none;
    width: 217px;

    background: #FFFFFF;
    border-radius: 10px 0px 10px 10px;

    position: absolute;
    top: calc(100% + 9px);
    right: 0;
    z-index: 2;

    @media(max-width:1023px) {
        width: 194px;
    }
}

.user__balance {
    display: flex;
    align-items: center;
    padding: 4px 10px 5px;
    margin-bottom: 5px;

    background: #EBF0F8;
    border-radius: 10px 0px 0px 0px;

    font-weight: 600;
    font-size: 16px;
    line-height: 190.18%;
    color: #545454;


    @media(max-width:1023px) {
        font-size: 14px;
        line-height: 27px;

        padding: 4px 15px 8px;
    }
}

.user__balance-sum {
    margin-left: 10px;

    font-weight: normal;
    font-size: 16px;
    line-height: 30px;
    color: #545454;


    @media(max-width:1023px) {
        font-size: 14px;
        line-height: 27px;
    }
}

.user__list {
    padding-bottom: 10px;
}

.user__item {
    transition: .2s linear;

    @media(min-width:1024px) {
        &:hover {
            background-color: #EBF0F8;
        }
    }

    @media(max-width:1023px) {
        &+& {
            margin-top: 5px;
        }
    }
}



.user__link {
    display: block;
    padding: 0 10px;

    font-weight: normal;
    font-size: 16px;
    line-height: 32px;
    color: #545454;

    transition: .2s linear;

    @media(min-width:1024px) {
        &:hover {
            color: black;
        }
    }

    @media(max-width:1023px) {
        font-size: 14px;
        line-height: 27px;
        padding: 0 10px 0 15px;
    }
}

.user__exit {
    display: block;
    padding: 0 10px;

    font-weight: normal;
    font-size: 16px;
    line-height: 32px;
    color: #FF4155;

    transition: .2s linear;

    @media(max-width:1023px) {
        font-size: 14px;
        line-height: 178.7%;

        padding: 0 10px 0 15px;
    }

    @media(min-width:1024px) {
        &:hover {
            color: red;
        }
    }
}

.menu-title {
    display: none;

    font-weight: bold;
    font-size: 16px;
    line-height: 22px;
    color: #545454;

    padding: 20px 0;
    margin-bottom: 20px;
    width: 292px;

    border-bottom: 1px solid #EEEEEE;

    @media(max-width:1023px) {
        display: block;
    }
}

.close-menu {
    display: none;

    @media(max-width:1023px) {
        display: block;

        position: absolute;
        top: 23px;
        right: 16px;
        z-index: 10;
    }

}

.burger {
    display: none;

    @media(max-width:1023px) {
        margin-left: 20px;
        display: block;
        width: 25px;
        height: 18px;

        img {
            object-fit: contain;
        }
    }
}

.user-mob {
    display: none;

    @media(max-width:1023px) {
        order: 1;
        align-items: center;
        flex-direction: row-reverse;
        justify-content: flex-end;
        margin-top: 18px;
        margin-bottom: 15px;
        padding-bottom: 20px;
        margin-left: 0;
        width: 292px;
        border-bottom: 1px solid #EEEEEE;
    }
}

.user-mob__icon {
    display: block;
    fill: #545454;
    width: 24px;
    height: 24px;
}