.support {
    position: relative;

    .affiliate-program__title {
        @media(max-width:767px) {
            text-align: center;
            margin-bottom: 36px;
        }
    }
}

.support__btn {
    position: absolute;
    top: 21px;
    right: 30px;

    @media(max-width:1023px) {
        display: none;
    }
}

.support__chat {
    display: flex;

    @media(max-width:1023px) {
        flex-direction: column;
    }
}

.support__chat-list {
    max-width: 348px;

    @media(max-width:1023px) {
        max-width: unset;
        margin-bottom: 30px;
        max-height: 263px;

        @include list-scroll;
    }
}

.support__chat-item {
    display: block;
    width: calc(100%);
    padding: 15px 20px 10px 10px;
    margin-right: 10px;

    background: #FFFFFF;
    border-radius: 10px;
    cursor: pointer;

    transition: .2s linear;

    &+& {
        margin-top: 10px;
    }

    &.active {
        background: #EBF0F8;
    }

    @media(min-width:1024px) {
        &:hover {
            background: #EBF0F8;
        }
    }

    @media(max-width:1023px) {
        width: calc(100% - 10px);
    }

    @media(max-width:767px) {
        padding: 10px;
    }
}

.support__chat-info {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.support__chat-logo {
    width: 26px;
    height: 26px;

    border-radius: 50%;
    overflow: hidden;

    @media(max-width:1023px) {
        width: 19px;
        height: 19px;
    }
}

.support__chat-name {
    font-weight: 600;
    font-size: 18px;
    line-height: 25px;
    color: #545454;

    margin-left: 10px;

    @media(max-width:1919px) {
        font-size: 16px;
        line-height: 23px;
    }

    @media(max-width:767px) {
        line-height: 16px;
    }
}

.support__chat-alert {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 27px;
    height: 27px;
    margin-left: 15px;

    background: #3990F1;
    border-radius: 50%;

    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #FFFFFF;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 16px;

        width: 22px;
        height: 22px;
        margin-left: 10px;
    }

    @media(max-width:767px) {
        width: 18px;
        height: 18px;

        font-size: 10px;
        line-height: 14px;
    }
}

.support__chat-time {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #8D8D8D;

    margin-left: auto;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 14px;
    }
}

.support__chat-text {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:767px) {
        line-height: 16px;
    }
}

.messenger {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 732px;
    height: 583px;
    padding: 15px;
    margin-left: 30px;

    background: #FFFFFF;
    border-radius: 10px;

    position: relative;

    @media(max-width:1919px) {
        height: 530px;
    }

    @media(max-width:1023px) {
        margin-left: 0;
        height: 390px;
        max-width: unset;
    }

    @media(max-width:767px) {
        padding: 10px;
    }
}

.messenger__nav {
    width: 100%;
    margin-top: auto;

    position: relative;

    @media(max-width:375px) {
        width: 267px;
    }
}

.messenger__textarea {
    display: block;
    width: 100%;
    height: 62px;
    padding: 20px 100px 20px 15px;

    border: 1px solid #EBF0F8;
    border-radius: 10px;
    resize: none;
    overflow: hidden;

    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #545454;

    &::placeholder {
        color: #D5D5D5;
    }


    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:767px) {
        height: 46px;
        padding: 15px 10px;
        line-height: 16px;
    }
}

.messenger__file {
    fill: #B7B7B7;
    cursor: pointer;
    transition: .2s linear;

    position: absolute;
    top: 23px;
    right: 68px;

    svg {
        width: 16px;
        height: 16px;

        @media(max-width:767px) {
            width: 11px;
            height: 11px;
        }
    }

    @media(min-width:1024px) {
        &:hover {
            fill: #545454;
        }
    }

    @media(max-width:767px) {
        top: 18px;
        right: 54px;
    }
}

.messenger__input {
    display: none;
}

.messenger__btn-submit {
    width: 38px;
    height: 38px;
    display: flex;
    align-items: center;
    justify-content: center;

    background: #EBF0F8;
    border-radius: 50%;
    fill: #78A1CF;

    transition: .2s linear;

    position: absolute;
    top: 12px;
    right: 15px;

    svg {
        width: 20px;
        height: 20px;

        @media(max-width:767px) {
            width: 15.26px;
            height: 15.26px;
        }
    }

    @media(min-width:1024px) {
        &:hover {
            fill: #3990F1;
        }
    }

    @media(max-width:767px) {
        width: 29px;
        height: 29px;

        top: 8.5px;
        right: 10px;
    }
}

.message__list {
    margin-bottom: 40px;
    max-height: 451px;

    overflow-y: scroll;

    &::-webkit-scrollbar {
        display: none;
    }

    -ms-overflow-style: none;
    /* IE and Edge */
    scrollbar-width: none;
    /* Firefox */
}

.message__item {
    display: flex;
    width: 100%;
    min-height: 72px;
    padding: 15px;

    background: #F8FAFD;
    border-radius: 10px;

    @media(max-width:767px) {
        padding: 5px;
        min-height: 42px;
    }

    &+& {
        margin-top: 15px;

        @media(max-width:1023px) {
            margin-top: 10px;
        }
    }

    &--user {
        background: #C7E1FF;

        .message__logo {
            img {
                max-width: 100%;
                height: auto;
                object-fit: contain;
            }
        }
    }
}

.message__logo {
    min-width: 38px;
    height: 38px;

    border-radius: 50%;

    @media(max-width:767px) {
        min-width: 28px;
        height: 28px;
    }
}

.message__text {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #545454;

    margin: 8px auto 0 15px;
    max-width: 558px;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
        margin-top: 0;
    }

    @media(max-width:767px) {
        line-height: 16px;
        margin-left: 5px;
    }
}

.message__date {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #8D8D8D;

    margin: 8px 0 0 20px;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
        margin-top: 0;
    }

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 14px;
        margin-left: 5px;
    }
}