.control-panel {
    padding-top: 10px;

    @media(max-width:1023px) {
        padding-top: 0;
    }

    @media(max-width:767px) {
        .order__results-group {
            padding-left: 0;
            padding-right: 0;
        }

        .order__results-btns {
            padding: 0;
            margin-bottom: 0;
        }
    }
}

.control-panel__list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 40px;

    @media(max-width:1919px) {
        margin-left: -5.5px;
        margin-right: -5.5px;
    }

    @media(max-width:1023px) {
        flex-wrap: wrap;
    }
}

.control-panel__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 203px;
    height: 131px;

    background: #FFFFFF;
    box-shadow: 0px 8.6871px 6.94968px rgba(6, 34, 132, 0.0413989), 0px 3.43489px 2.74791px rgba(6, 34, 132, 0.06);
    border-radius: 10px;

    @media(max-width:1919px) {
        max-width: 203px;
        margin: 0 5.5px;
        height: 110px;
    }

    @media(max-width:1023px) {
        max-width: unset;
        width: calc(100% / 3 - 11px);
        height: 110px;
    }

    @media(max-width:767px) {
        height: 76px;
    }

    &:last-child {
        width: 411px;
        padding: 16px 20px 17px;
        align-items: flex-start;
        position: relative;

        @media(max-width:1919px) {
            max-width: 411px;
        }

        @media(max-width:1023px) {
            max-width: unset;
            width: 100%;
            margin-top: 11px;
        }

        @media(max-width:767px) {
            height: auto;
            padding: 7px 10px;
        }
    }
}

.control-panel__value {
    font-weight: bold;
    font-size: 26px;
    line-height: 59px;
    text-align: center;
    color: #78A1CF;

    @media(max-width:1919px) {
        font-size: 21px;
        line-height: 54px;
    }

    @media(max-width:1439px) {
        font-size: 16px;
        line-height: 30px;
    }
}

.control-panel__text {
    font-weight: bold;
    font-size: 16px;
    line-height: 36px;
    text-align: center;
    color: #78A1CF;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 28px;
    }

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 22px;
    }

    &--pc {
        @media(max-width:767px) {
            display: none;
        }
    }

    &--mob {
        display: none;

        @media(max-width:767px) {

            display: block;
        }
    }
}

.control-panel__promo-title {
    font-weight: bold;
    font-size: 18px;
    line-height: 175.18%;
    color: #78A1CF;

    margin-bottom: 10px;

    @media(max-width:1919px) {
        font-size: 16px;
        line-height: 26px;
    }

    @media(max-width:1439px) {
        font-size: 14px;
        line-height: 24px;
        margin-bottom: 5px;
    }
}

.control-panel__promo-descr {
    max-width: 317px;

    font-weight: normal;
    font-size: 16px;
    line-height: 175.18%;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 22px;
    }

    @media(max-width:1439px) {
        font-size: 14px;
        line-height: 18px;
    }
}

.control-panel__promo-link {
    width: 59px;
    height: 59px;

    position: absolute;
    top: 14px;
    right: 21px;

    transition: .2s linear;

    @media(min-width:1024px) {
        &:hover {
            transform: scale(1.1);
        }
    }

    @media(max-width:1919px) {
        width: 49px;
        height: 49px;
    }

    @media(max-width:1439px) {
        width: 36px;
        height: 36px;
    }

    @media(max-width:767px) {
        width: 23px;
        height: 23px;

        left: 171px;
        right: unset;
        top: 11px;
    }
}

.control-panel__subtitle {
    display: none;

    @media(max-width:767px) {
        display: block;
        margin-bottom: 30px;

        font-weight: normal;
        font-size: 14px;
        line-height: 16px;
        text-align: center;
        color: #78A1CF;
    }
}