.policy__title {
    max-width: 735px;

}

.policy__content {
    @media(max-width:767px) {
        margin: 40px 0 30px;
    }

    b {
        font-weight: 700;
        font-size: 16px;
        line-height: 22px;
        color: #545454;


        @media(max-width:1919px) {
            font-size: 14px;
            line-height: 18px;
        }

        @media(max-width:767px) {
            line-height: 16px;
        }
    }

    p {
        font-weight: 600;
        font-size: 16px;
        line-height: 22px;
        color: #545454;

        margin-bottom: 20px;

        @media(max-width:1919px) {
            font-size: 14px;
            line-height: 18px;
            margin-bottom: 15px;
        }

        @media(max-width:767px) {
            line-height: 16px;
            margin-bottom: 10px;
        }
    }
}