.rating {
    display: flex;

    &--small {}
}

.rating__wrapper {
    display: inline-flex;
    align-items: center;
    flex-direction: row-reverse;
    margin-right: 4px;
}

.rating__item {
    fill: #C4C4C4;

    &+& {
        margin-right: 5px;
    }

    @media(max-width:767px) {
        fill: #F5AD4A;

        &:nth-child(1n + 2) {
            display: none;
        }
    }
}

.rating__icon {
    width: 24px;
    height: 24px;

    @media(max-width:1919px) {
        width: 17px;
        height: 17px;
    }


    &--reviews {
        width: 17px;
        height: 17px;
    }
}

.rating__total,
.rating__current {
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    color: #696969;
}

.rating__total {
    display: none;

    @media(max-width:767px) {
     display: block;
    }
}

// .rating__current {}

.rating__wrapper[data-total-value='1'] .rating__item:nth-child(n + 5),
.rating__wrapper[data-total-value='2'] .rating__item:nth-child(n + 4),
.rating__wrapper[data-total-value='3'] .rating__item:nth-child(n + 3),
.rating__wrapper[data-total-value='4'] .rating__item:nth-child(n + 2),
.rating__wrapper[data-total-value='5'] .rating__item:nth-child(n + 1) {
    fill: #F5AD4A;
}