.settings__subtitle {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #78A1CF;

    margin-bottom: 40px;

    @media(max-width:1919px) {
        font-size: 16px;
        line-height: 23px;
        margin-bottom: 32px;
    }

    @media(max-width:1439px) {
        font-size: 14px;
        line-height: 19px;
        margin-bottom: 28px;
    }

    @media(max-width:767px) {
        text-align: center;
    }
}

.settings__content {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.settings__password {
    width: calc(50% - 15px);
    margin-bottom: 40px;

    @media(max-width:1023px) {
        width: calc(50% - 9px);
    }

    @media(max-width:767px) {
        width: 51%;
        min-width: 308px;
        margin-bottom: 25px;
        margin-left: auto;
        margin-right: auto;
    }
}

.settings__content-title {
    font-weight: bold;
    font-size: 18px;
    line-height: 25px;
    color: #545454;

    margin-bottom: 33px;

    @media(max-width:1919px) {
        font-size: 16px;
        line-height: 23px;
        margin-bottom: 25px;
    }

    @media(max-width:1439px) {
        font-size: 14px;
        line-height: 19px;
        margin-bottom: 20px;
    }

    @media(max-width:767px) {
        text-align: center;
    }
}

.settings__group {
    &+& {
        margin-top: 20px;

        @media(max-width:767px) {
            margin-top: 15px;
        }
    }
}

.settings__name {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #545454;

    margin-bottom: 15px;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:1439px) {
        margin-bottom: 10px;
        font-size: 14px;
        line-height: 16px;
    }
}

.settings__info {
    width: calc(50% - 15px);
    margin-bottom: 40px;

    @media(max-width:1023px) {
        width: calc(50% - 9px);
    }

    @media(max-width:767px) {
        width: 51%;
        min-width: 308px;
        margin-bottom: 25px;
        margin-left: auto;
        margin-right: auto;
    }
}

.settings__notification {
    width: 100%;
}


.settings__board {
    background: #FFFFFF;
    border-radius: 10px;
}

.settings__board-top {
    display: flex;
    align-items: center;
    padding: 13px 41px 12px 17px;

    background: #EBF0F8;
    border-radius: 10px 10px 0px 0px;

    @media(max-width:1919px) {
        justify-content: space-between;
    }

    @media(max-width:767px) {
        padding: 8px 5px;
    }

    .settings__column {
        font-weight: 600;
    }

}

.settings__column {
    width: 100%;

    color: #545454;
    font-size: 16px;
    line-height: 22px;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:1439px) {
        font-size: 13px;
        line-height: 16px;
    }

    &:nth-child(1) {
        max-width: 198px;
    }

    &:nth-child(2) {
        max-width: 168px;
        margin-left: 60px;

        @media(max-width:767px) {
            text-align: center;
        }
    }

    &:nth-child(3) {
        max-width: 99px;
        margin-left: 187px;

        @media(max-width:767px) {
            text-align: center;
        }
    }

    &:nth-child(4) {
        max-width: 153px;
        margin-left: auto;

        @media(max-width:767px) {
            text-align: center;
        }
    }

    @media(max-width:1919px) {
        width: 25% !important;
        margin: 0 !important;
    }

    span {
        font-size: inherit;
        font-weight: inherit;
        color: inherit;
        line-height: inherit;

        &:nth-child(1) {
            @media(max-width:767px) {
                display: none;
            }
        }

        &:nth-child(2) {
            display: none;

            @media(max-width:767px) {
                display: block;
            }
        }
    }
}


.settings__row {
    display: flex;
    align-items: center;
    padding: 15px 41px 15px 17px;

    @media(max-width:1919px) {
        justify-content: space-between;
    }

    @media(max-width:767px) {
        padding: 10px 5px 0;
        &:last-child {
            padding-bottom: 10px;
        }
    }

    .settings__column {
        font-weight: 400;

        @media(max-width:767px) {
            font-size: 10px;
            line-height: 14px;
        }
    }

}

.settings__btn {
    margin-top: 30px;

    @media(max-width:767px) {
        margin: 15px auto 0;
    }
}