.questions {
    margin-bottom: 60px;

    @media(max-width:1919px) {
        margin-bottom: 40px;
    }

    @media(max-width:1439px) {
     margin-bottom: 0;
    }

    @media(max-width:767px) {
        margin-bottom: 30px;
    }
}

.questions__title {
    margin-bottom: 60px;

    @media(max-width:1439px) {
        margin-bottom: 40px;
    }

    @media(max-width:767px) {
        margin-bottom: 30px;
    }
}

.questions__link {
    display: inline-block;
    margin-left: 5px;

    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    text-align: center;
    color: #78A1CF;

    position: relative;
    overflow: hidden;

    &::before {
        content: '';
        height: 1px;
        background-color: #78A1CF;

        transition: .2s linear;

        position: absolute;
        left: 0;
        right: 0;
        bottom: 1px;
    }

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(min-width:1024px) {
        &:hover::before {
            transform: translateX(100%);
        }
    }

    @media(max-width:767px) {
        line-height: 16px;
    }
}

.questions__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px;
    align-items: start;

    @media(max-width:1919px) {
        grid-gap: 20px;
    }

    @media(max-width:1439px) {
        grid-gap: 15px;
    }

    @media(max-width:767px) {
        grid-template-columns: repeat(1, 1fr);
        grid-gap: 10px;
    }
}

.questions__item {
    padding: 0 20px;

    background: #F8FAFD;
    border-radius: 10px;

    @media(max-width:1919px) {
        padding: 0 15px;
    }

    @media(max-width:767px) {
        padding: 0 10px;
    }
}

.question__content {
    display: flex;
    align-items: center;
    width: 100%;
    height: 74px;
    padding-right: 12px;

    font-weight: normal;
    font-size: 18px;
    line-height: 25px;
    color: #545454;

    transition: .3s linear;
    cursor: pointer;

    position: relative;

    @media(max-width:1919px) {
        font-size: 16px;
        line-height: 20px;
        height: 65px;
    }

    @media(max-width:1439px) {
        font-size: 14px;
        line-height: 18px;
        height: 55px;
    }

    @media(max-width:767px) {
        line-height: 16px;
        height: 48px;
    }

    &::before {
        content: '';
        width: 9px;
        height: 11px;
        background-image: url("../img/triangle.svg");
        background-repeat: no-repeat;
        background-size: 100% 100%;


        position: absolute;
        top: 33px;
        right: 0;

        transition: .3s linear;

        @media(max-width:1919px) {
            top: 29px;
        }

        @media(max-width:1439px) {
            top: 22px;
        }

        @media(max-width:767px) {
            top: 21px;
            width: 6px;
            height: 8px;
        }
    }

    &.active {
        color: #78A1CF;

        &::before {
            transform: rotate(180deg);
        }
    }
}

.question__descr {
    display: none;
    margin: 6px 0 25px;

    font-weight: normal;
    font-size: 18px;
    line-height: 25px;
    color: #696969;

    @media(max-width:1919px) {
        font-size: 16px;
        line-height: 20px;
        margin: 6px 0 20px;
    }

    @media(max-width:1439px) {
        font-size: 14px;
        line-height: 18px;
        margin: 0px 0 15px;
    }

    @media(max-width:767px) {
        line-height: 16px;
    }
}