.form__group {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;

    margin-bottom: 20px;

    &--password {
        margin-bottom: 25px;

        position: relative;
    }
    
    @media(max-width:767px) {
        margin-bottom: 10px;
    }
}

.captcha-fake {
    height: 60px;
}

.form__name {
    font-weight: bold;
    font-size: 18px;
    line-height: 25px;
    color: #545454;

    margin-bottom: 15px;

    @media(max-width:1919px) {
        font-size: 16px;
        line-height: 21px;
    }

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 19px;
    }
}

.btn__view-password {
    width: 20px;
    height: 20px;

    position: absolute;
    bottom: 18px;
    right: 15px;

    @media(max-width:1919px) {
        bottom: 12px;
    }

    @media(max-width:1023px) {
        bottom: 13px;
        width: 15px;
        height: 13px;

        img {
            object-fit: contain;
        }
    }

    &.show {
        &::before {
            content: '';
            position: absolute;
            height: 1px;
            width: 20px;
            background-color: #696969;

            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%) rotate(45deg);

            @media(max-width:767px) {
                width: 15px;
            }
        }
    }
}