.footer {
    display: flex;
    align-items: flex-end;
    height: 696px;
    padding-top: 320px;
    padding-bottom: 30px;
    margin-top: auto;

    background-image: url('../img/footer-bg.svg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top;

    @media(min-width:1921px) {
        height: 726px;
    }

    @media(max-width:1919px) {
        padding-top: 230px;
        height: 590px;
    }

    @media(max-width:1439px) {
        padding-top: 170px;
        height: 480px;
    }

    @media(max-width:767px) {
        height: auto;
        padding-top: 84px;
        background-image: url('../img/footer-bg-mob2.svg');
    }

    @media(max-width:375px) {
        background-image: url('../img/footer-bg-mob.svg');
    }
}

.footer__top {
    display: flex;
    margin-bottom: 40px;

    @media(max-width:1187px) {
        justify-content: space-between;
    }

    @media(max-width:1439px) {
        margin-bottom: 30px;
    }

    @media(max-width:767px) {
        flex-direction: column;
        align-items: center;
    }
}

.footer__intro {
    margin-right: 87px;

    @media(max-width:1187px) {
        margin-right: 0;
    }

    @media(max-width:767px) {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 27px;
    }
}

.footer__logo {
    display: block;
    width: 205.7px;
    height: 48.02px;
    margin-bottom: 15px;

    img {
        height: auto;
    }

    @media(max-width:1919px) {
        width: 175.7px;
        height: 42.02px;
    }

    @media(max-width:1023px) {
        width: 160.3px;
        height: 37.42px;
        margin-bottom: 10px;
    }
}

.footer__descr {
    font-weight: normal;
    font-size: 14px;
    line-height: 19px;
    color: #FFFFFF;

    margin-bottom: 20px;
    max-width: 213px;

    @media(max-width:1919px) {
        line-height: 17px;
    }

    @media(max-width:1023px) {
        font-size: 13px;
        line-height: 14px;
    }

    @media(max-width:767px) {
        text-align: center;
    }
}

.footer__block {
    display: flex;
}

.footer__social-link {
    display: block;
    width: 40px;
    height: 40px;

    transition: .2s linear;

    &+& {
        margin-left: 20px;
    }

    @media(max-width:1439px) {
        width: 35px;
        height: 35px;
    }

    @media(max-width:1023px) {
        width: 30px;
        height: 30px;
    }

    @media(min-width:1024px) {
        &:hover {
            transform: scale(1.1);
        }
    }
}

.footer__nav {
    display: flex;
    margin-top: 12px;

    @media(max-width:767px) {
        margin-top: 0;
        margin-bottom: 20px;
    }
}

.footer__nav-block {
    &+& {
        margin-left: 133px;

        @media(max-width:1187px) {
            margin-left: 31px;
        }

        @media(max-width:767px) {
            margin-left: 51px;
        }
    }
}

.footer__title {
    font-weight: bold;
    font-size: 22px;
    line-height: 30px;
    color: #FFFFFF;

    margin-bottom: 20px;

    @media(max-width:1919px) {
        font-size: 18px;
        line-height: 26px;
        margin-bottom: 15px;
    }

    @media(max-width:1439px) {
        font-size: 16px;
        line-height: 22px;
        margin-bottom: 12px;
    }

    @media(max-width:1023px) {
        font-size: 14px;
        line-height: 19px;

        margin-bottom: 10px;
    }
}

.footer__nav-link {
    display: block;

    font-weight: normal;
    font-size: 18px;
    line-height: 206.2%;
    color: #FFFFFF;

    transition: .2s linear;

    @media(min-width:1024px) {
        &:hover {
            opacity: .7;
        }
    }

    @media(max-width:1919px) {
        font-size: 16px;
    }

    @media(max-width:1439px) {
        line-height: 186.2%;
        font-size: 14px;
    }

    @media(max-width:1023px) {
        line-height: 206.2%;
        font-size: 13px;
    }

    @media(max-width:767px) {
        font-size: 14px;
    }
}

.footer__payment {
    margin-left: 102px;
    margin-top: 12px;

    @media(max-width:1187px) {
        margin-left: 0;
    }

    @media(max-width:767px) {
        margin-top: 0;
    }
}

.footer__payment-list {
    display: grid;
    grid-template-columns: repeat(4, 45px);
    grid-gap: 5px;

    @media(max-width:1023px) {
        margin-top: 15px;
    }

    @media(max-width:767px) {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        max-width: 204px;
    }
}

.footer__payment-item {
    height: 26px;
}

.footer__bottom {
    border-top: 1px solid #5F5F5F;
    padding-top: 30px;

    @media(max-width:1439px) {
        padding-top: 20px;
    }
}

.footer__wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 15px;

    @media(max-width:767px) {
        flex-wrap: wrap;
        margin-left: auto;
        margin-right: auto;
        max-width: 270px;
    }
}

.footer__another-link {
    display: block;

    font-weight: normal;
    font-size: 14px;
    line-height: 206.18%;
    color: #78A1CF;

    overflow: hidden;
    position: relative;

    &::before {
        content: '';
        height: 1px;
        background-color: #78A1CF;

        transition: .2s linear;

        position: absolute;
        left: 0;
        right: 0;
        bottom: 7px;

        @media(max-width:1023px) {
            bottom: 5px;
        }

        @media(max-width:767px) {
            bottom: 7px;
        }
    }

    &+& {
        margin-left: 22px;

        @media(max-width:767px) {
            margin-left: 0;
        }
    }

    &:hover {
        color: #78A1CF;

        @media(min-width:1024px) {
            &::before {
                transform: translateX(100%);
            }
        }
    }

    @media(max-width:767px) {
        &:nth-child(1) {
            width: 50%;
            text-align: left;
        }
        &:nth-child(2) {
            width: 50%;
            text-align: right;
        }
    }

}

.footer__copyright {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    text-align: center;
    color: #8F8F8F;

    @media(max-width:1439px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:767px) {
        line-height: 16px;
    }
}