.select {
    position: relative;
    width: 100%;

    &--referral {
        margin-left: 10px;

        .select__top {
            width: 66px;
            height: 32px;

            @media(max-width:767px) {
                width: 44px;
                height: 26px;
            }

            &::before {
                top: 13px;
                right: 10px;

                width: 7px;
                height: 9px;

                @media(max-width:767px) {
                    width: 5px;
                    height: 6px;

                    top: 11px;
                    right: 6px;
                }
            }
        }

        .select__main-input {
            padding: 5px 10px;

            background-color: #F8FAFD;

            font-weight: normal;
            font-size: 16px;
            line-height: 22px;
            color: #545454;

            @media(max-width:1599px) {
                font-size: 14px;
                line-height: 18px;
            }

            @media(max-width:1023px) {
                background-color: #fff;
            }
        }

        .select__content-item {
            padding: 5px 10px;

            @media(max-width:767px) {
                padding: 12px 10px;
            }
        }
    }

    &--grey {

        .select__main-input,
        .select__content {
            background: #F8FAFD;

            @media(max-width:767px) {
                background: #fff;
            }
        }
    }

    &--with-img {
        .select__main-input {
            padding-left: 51px;
            text-transform: capitalize;

            @media(max-width:1599px) {
             padding-left: 43px;
            }
        }
    }
}

.select__top {
    display: block;
    width: 100%;
    height: 57px;

    position: relative;

    @media(max-width:1919px) {
     height: 47px;
    }

    @media(max-width:767px) {
        height: 39px;
    }

    &::before {
        content: '';
        width: 9px;
        height: 11px;

        background-image: url("../img/triangle.svg");
        background-repeat: no-repeat;
        background-size: 100% 100%;

        position: absolute;
        top: 24px;
        right: 14px;

        transition: .3s linear;

        @media(max-width:1919px) {
            top: 18px;
        }

        @media(max-width:767px) {
            width: 6px;
            height: 8px;
            top: 16px;
        }
    }

    &.active::before {
        transform: rotate(180deg);
    }
}

.select__main-img {
    width: 21px;
    height: 21px;

    position: absolute;
    top: 50%;
    left: 15px;
    transform: translateY(-50%);
}

.select__main-input {
    display: block;
    width: 100%;
    height: 100%;
    padding: 16px 15px;

    cursor: pointer;
    background-color: #FFFFFF;
    border-radius: 10px;
    border: none;

    font-family: "Open Sans";
    font-weight: normal;
    font-size: 18px;
    line-height: 25px;
    color: #696969;

    @media(max-width:1919px) {
        font-size: 16px;
        line-height: 23px;
    }

    @media(max-width:1599px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:1023px) {
        padding: 11px 10px;
    }

    &::placeholder {
        color: #D5D5D5;
    }
}

.select__content {
    padding: 0;
    max-height: 0;

    transition: .3s linear;

    &.active {
        max-height: 237px;
        padding: 7.5px 0;
        z-index: 20;

        @media(max-width:767px) {
            max-height: 187px;
        }
    }

    overflow: scroll;

    &::-webkit-scrollbar {
        display: none;
    }

    -ms-overflow-style: none;
    /* IE and Edge */
    scrollbar-width: none;
    /* Firefox */

    position: absolute;
    top: calc(100% + 4px);
    left: 0;
    right: 0;
    z-index: 10;

    background: #FFFFFF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
}

.select__content-item {
    display: block;
    cursor: pointer;
    padding: 12px 15px;

    transition: .2s linear;

    @media(min-width:1024px) {
        &:hover {
            background-color: #4E90D9;

            .select__content-name {
                color: #fff;
            }
        }
    }
}

.select__content-input {
    display: none;

    &:checked+.select__content-box {
        .select__content-name {
            font-weight: 600;
            color: #000;
        }
    }
}

.select__content-box {
    display: flex;
    align-items: center;
}

.select__content-img {
    width: 21px;
    height: 21px;
    margin-right: 15px;
}

.select__content-name {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #696969;
    text-transform: capitalize;

    transition: .2s linear;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
    }
}