.benefits {
    padding-top: 120px;
    height: 1153px;
    margin-bottom: 9px;
    position: relative;

    &::before {
        content: '';
        background-image: url("../img/benefits-bg.svg");
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;

        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: -1;

        @media(max-width:375px) {
            top: 110px;
            background-size: contain;
            background-image: url("../img/benefits-bg-mob.svg");
        }
    }

    position: relative;

    @media(max-width:1919px) {
        height: 900px;
        padding-top: 80px;
    }

    @media(max-width:1599px) {
        height: 850px;
    }

    @media(max-width:1439px) {
        height: 750px;
    }

    @media(max-width:767px) {
        height: auto;
        padding-top: 60px;
        padding-bottom: 127px;
    }

    @media(max-width:375px) {
        padding-bottom: 87px;
        margin-bottom: 40px;
    }
}


.benefits__title {
    margin-bottom: 173px;

    @media(max-width:1919px) {
        margin-bottom: 77px;
    }

    @media(max-width:1439px) {
        margin-bottom: 50px;
    }
}

.benefits__list {
    /* display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 570px; */
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px;
    width: 570px;
    margin-left: auto;

    @media(max-width:1023px) {
        margin: 0 auto;
    }

    @media(max-width:767px) {
        width: 336px;
        grid-gap: 36px 33px;
    }
}


.benefits__icon {
    width: 96px;
    height: 96px;
    margin-bottom: 30px;
    padding: 21px 22px 22px 21px;

    background-color: #F8FAFD;
    box-shadow: 0px 8.6871px 6.94968px rgba(6, 34, 132, 0.0413989), 0px 3.43489px 2.74791px rgba(6, 34, 132, 0.06);
    border-radius: 10px;

    @media(max-width:1919px) {
        width: 86px;
        height: 86px;
        margin-bottom: 20px;
    }

    @media(max-width:1439px) {
        margin: 0 auto 20px;
    }

    @media(max-width:767px) {
        margin-bottom: 15px;
    }
}

.benefits__name {
    font-weight: bold;
    font-size: 22px;
    line-height: 30px;
    color: #545454;

    margin-bottom: 20px;

    @media(max-width:1919px) {
        font-size: 18px;
        line-height: 26px;
        margin-bottom: 15px;
    }

    @media(max-width:1439px) {
        font-size: 16px;
        line-height: 20px;
        margin-bottom: 10px;
        text-align: center;
    }

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 19px;

        margin-bottom: 12px;
    }
}

.benefits__descr {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #696969;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:1439px) {
        text-align: center;
    }

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 16px;
    }
}

.benefits__photo {
    width: 868px;
    height: 600px;

    position: absolute;
    bottom: 231px;
    left: 0;

    @media(max-width:1919px) {
        width: 38vw;
        height: auto;
        bottom: 28%;
    }


    @media(max-width:1023px) {
        display: none;
    }
}