.purchase-history__tabs {
    display: flex;
    align-items: center;
    margin-bottom: 20px;

    @media(max-width:767px) {
        justify-content: space-between;
        margin-bottom: 15px;
    }
}

.purchase-history__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 171px;
    height: 57px;


    background: #FFFFFF;
    border-radius: 10px;

    transition: .2s linear;

    &+& {
        margin-left: 20px;

        @media(max-width:1023px) {
            margin-left: 10px;
        }

        @media(max-width:767px) {
            margin-left: 0;
        }
    }

    &.active {
        background: #EBF0F8;
    }

    @media(min-width:1024px) {
        &:hover:not(.active) {
            transform: scale(.9);
        }
    }

    @media(max-width:1439px) {
        height: 47px;
    }

    @media(max-width:767px) {
        width: 99px;
        height: 38px;
    }
}

.purchase-history__btn-text {
    padding-left: 28px;

    font-weight: normal;
    font-size: 18px;
    line-height: 25px;
    color: #545454;

    position: relative;

    @media(max-width:1919px) {
        font-size: 16px;
        line-height: 23px;
    }

    @media(max-width:1439px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:767px) {
        padding-left: 0;
        font-size: 14px;
        line-height: 16px;
    }

    &::before {
        content: '';
        width: 23px;
        height: 23px;

        background-image: url('../img/sort.svg');

        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);

        @media(max-width:767px) {
            display: none;
        }
    }
}