.information {
    display: none;
    width: 100%;
    padding: 30px 20px 89px 30px;

    background: #F8FAFD;
    border-radius: 10px;

    position: relative;

    @media(max-width:1919px) {
        padding: 20px 15px 30px 15px;
    }

    @media(max-width:767px) {
        padding: 15px 10px;
    }

    &.show {
        display: block;

        animation: fade .7s linear;
    }
}

.information__btn {
    width: 186px;

    position: absolute;
    left: 30px;
    top: 27px;

    @media(max-width:1919px) {
        width: 166px;
        left: 15px;
        top: 17px;
    }

    @media(max-width:1023px) {
        width: 138px;
        left: 10px;
    }

    @media(max-width:767px) {
        left: 50%;
        top: unset;
        bottom: 15px;
        transform: translateX(-50%);
    }

    &--mob {
        display: none;

        @media(max-width:767px) {
            display: block;
            width: 36px;
            height: 36px;

            background-color: transparent;
            background-image: url(../img/btn.svg);

            bottom: unset;
            top: 11px;
            left: 10px;
            transform: unset;
        }
    }
}

.information__title {
    font-weight: 600;
    font-size: 24px;
    line-height: 33px;
    text-align: center;
    color: #545454;

    margin-bottom: 38px;

    @media(max-width:1919px) {
        font-size: 20px;
        line-height: 28px;
        margin-bottom: 30px;
    }

    @media(max-width:767px) {
        margin-bottom: 20px;

        font-size: 20px;
        line-height: 27px;
    }

    @media(max-width:414px) {
        padding-left: 20px;
    }
}

.information__list {
    max-height: 492px;

    overflow-y: scroll;
    @include list-scroll;

    @media(max-width:767px) {
        margin-bottom: 56px;
    }
}

.information__item {
    margin-right: 30px;
    padding: 20px 15px;

    border-radius: 10px;
    background: #FFFFFF;

    @media(max-width:1919px) {
        margin-right: 15px;
        padding: 15px 15px;
    }

    @media(max-width:1439px) {
        padding: 12px;
    }

    @media(max-width:767px) {
        margin-right: 10px;
        padding: 10px;
    }

    &+& {
        margin-top: 15px;

        @media(max-width:767px) {
            margin-top: 10px;
        }
    }
}

.information__name {
    font-weight: bold;
    font-size: 22px;
    line-height: 30px;
    color: #545454;

    margin-bottom: 19px;

    @media(max-width:1919px) {
        font-size: 18px;
        line-height: 24px;
        margin-bottom: 14px;
    }

    @media(max-width:1439px) {
        font-size: 16px;
        line-height: 20px;
        margin-bottom: 12px;
    }

    @media(max-width:767px) {
        margin-bottom: 10px;

        font-size: 14px;
        line-height: 19px;
    }
}

.information__text {
    font-weight: normal;
    font-size: 18px;
    line-height: 25px;
    color: #545454;

    margin-bottom: 18px;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
        margin-bottom: 14px;
    }

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 16px;

        margin-bottom: 10px;
    }
}

.information__descr {
    display: flex;
    align-items: center;

    @media(max-width:767px) {
        flex-direction: column;
        align-items: flex-start;
    }
}

.information__descr-item {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 16px;
    }

    &+& {
        margin-left: 60px;

        @media(max-width:767px) {
            margin-left: 0;
            margin-top: 10px;
        }
    }
}