* {
    font-family: 'Open Sans', sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 25px;
    color: #FFFFFF;
}

body {
    background: #FFF;

    &.scroll-hide {
        overflow: hidden;
    }
}

.container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 15px;

    @media(max-width:767px) {}
}

.page {
    display: flex;
    flex-direction: column;
    min-height: 100vh;

    position: relative;
    overflow: hidden;
}

img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.main-title {
    font-weight: 800;
    font-size: 40px;
    line-height: 54px;

    margin-bottom: 25px;

    @media(max-width:1919px) {
        font-size: 30px;
        line-height: 36px;
    }

    @media(max-width:767px) {
        font-size: 22px;
        line-height: 30px;
        margin-bottom: 15px;
    }
}

.subtitle {
    font-weight: 400;
    font-size: 18px;
    line-height: 25px;

    @media(max-width:1919px) {
        font-size: 16px;
        line-height: 23px;
    }

    @media(max-width:1599px) {
        max-width: 417px;
        font-size: 14px;
        line-height: 20px;
    }

    /* 
    @media(max-width:767px) {
        font-size: 14px;
        line-height: 16px;
    } */
}

.title {
    h2 {
        margin-bottom: 10px;

        font-weight: 600;
        font-size: 30px;
        line-height: 41px;
        text-align: center;
        color: #545454;

        @media(max-width:1919px) {
            font-size: 26px;
            line-height: 36px;
        }

        @media(max-width:1439px) {
            font-size: 24px;
            line-height: 30px;
            margin-bottom: 8px;
        }

        @media(max-width:767px) {
            font-size: 20px;
            line-height: 27px;
            margin-bottom: 15px;
        }
    }

    p {
        display: flex;
        justify-content: center;

        font-weight: normal;
        font-size: 16px;
        line-height: 22px;
        text-align: center;
        color: #78A1CF;

        @media(max-width:1919px) {
            font-size: 14px;
            line-height: 18px;
        }
    }
}


.scale {
    @media(min-width:1024px) {
        transition: transform .2s linear;

        &:hover {
            transform: scale(1.1);
        }
    }
}

.background {
    height: 637px;
    padding-top: 196px;

    background-image: url('../img/header-bg.svg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: bottom;

    @media(max-width:1919px) {
        height: 587px;
    }

    @media(max-width:1439px) {
        height: 540px;
    }

    @media(max-width:1023px) {
        height: 450px;
        padding-top: 118px;
    }

    @media(max-width:767px) {
        background-image: url('../img/header-bg-mob2.svg');
        height: 359px;
    }

    @media(max-width:375px) {
        background-image: url('../img/header-bg-mob.svg');
    }
}

@keyframes fade {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}