.confirmation {
    padding: 30px 30px 44px;
    background: #F8FAFD;
    border-radius: 10px;

    position: relative;

    @media(max-width:767px) {
        padding: 20px 10px 10px;
    }
}

.confirmation__inner {
    display: flex;
    width: 100%;
    justify-content: space-between;

    @media(max-width:1023px) {
        flex-direction: column;
    }
}

.confirmation__title {
    margin-bottom: 40px;

    font-weight: 600;
    font-size: 30px;
    line-height: 41px;
    text-align: center;
    color: #545454;

    @media(max-width:767px) {
        font-size: 20px;
        line-height: 27px;
        margin-bottom: 30px;
    }
}

.confirmation__info {
    @media(max-width:1023px) {
        margin-bottom: 30px;
    }
}

.confirmation__data {
    margin-bottom: 30px;
}

.confirmation__subtitle {
    font-weight: bold;
    font-size: 22px;
    line-height: 30px;
    color: #545454;

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 19px;
    }

    &--data {
        margin-bottom: 30px;
        padding-right: 54px;
        position: relative;
        width: fit-content;

        @media(max-width:767px) {
            margin-bottom: 18px;
            padding-right: 39px;
        }

        &::before {
            content: '';
            width: 34px;
            height: 34px;

            background-image: url("../img/edit.svg");

            position: absolute;
            top: 50%;
            right: 0;
            transform: translateY(-50%);

            @media(max-width:767px) {
                width: 24px;
                height: 24px;
                background-size: cover;
            }
        }
    }

    &--payment {
        margin-bottom: 20px;

        @media(max-width:767px) {
            text-align: center;
        }
    }

    &--descr {
        margin-bottom: 30px;

        @media(max-width:767px) {
            margin-bottom: 20px;
        }
    }
}

.confirmation__data-item {
    font-weight: bold;
    font-size: 16px;
    line-height: 22px;
    color: #545454;

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 16px;
    }

    &+& {
        margin-top: 15px;
    }
}

.confirmation__data-value {
    display: inline-block;
    margin-left: 10px;

    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #545454;

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 16px;
    }
}

.confirmation__paymen-item {
    display: flex;
    align-items: center;

    &+& {
        margin-top: 20px;

        @media(max-width:767px) {
            margin-top: 15px;
        }
    }

    &.active {
        .confirmation__paymen-name {
            font-weight: bold;
            color: #3990F1;
        }

        .confirmation__paymen-commission {
            color: #78A1CF;
        }
    }
}

.confirmation__paymen-icon {
    max-width: 43px;
    max-height: 43px;
    margin-right: 15px;

    img {
        object-fit: contain;
    }

    @media(max-width:767px) {
        max-width: 40px;
        max-height: 40px;
    }
}

.confirmation__paymen-name {
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    color: #545454;

    margin-bottom: 4px;

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 19px;
        margin-bottom: 7px;
    }
}

.confirmation__paymen-commission {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #696969;

    @media(max-width:767px) {
        font-size: 10px;
        line-height: 14px;
    }
}

.confirmation__btn {
    width: unset;
    padding: 0 10px;

    @media(min-width:1024px) {
        position: absolute;
        right: 30px;
        bottom: 20px;
    }

    @media(max-width:1023px) {
        margin-top: 20px;
    }

    @media(max-width:767px) {
        margin: 20px auto 0 auto;
    }
}

.confirmation__descr {
    max-width: 540px;
    width: 100%;
    padding: 20px 30px;
    margin-bottom: 49px;

    background: #FFFFFF;
    border-radius: 10px;

    @media(max-width:1919px) {
        max-width: 450px;
    }

    @media(max-width:1023px) {
        margin-bottom: 0;
    }

    @media(max-width:767px) {
        max-width: unset;
        padding: 10px;
    }
}

.confirmation__descr-title {
    margin-bottom: 10px;

    font-weight: normal;
    font-size: 18px;
    line-height: 25px;
    color: #78A1CF;

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 19px;
    }
}

.confirmation__descr-text {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #545454;

    margin-bottom: 20px;

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 16px;

        margin-bottom: 15px;
    }
}

.confirmation__descr-item {
    padding-left: 45px;

    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #545454;

    position: relative;

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 16px;

        padding-left: 30px;
    }

    &::before {
        content: '';
        width: 30px;
        height: 30px;

        background-image: url("../img/check.svg");

        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);

        @media(max-width:767px) {
            width: 20px;
            height: 20px;
            background-size: cover;
        }
    }

    &+& {
        margin-top: 28px;

        @media(max-width:767px) {
            margin-top: 12px;
        }
    }
}