.popup {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;

    background: rgba(0, 0, 0, 0.25);

    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: calc(var(--vh, 1vh) * 100);

    &--sign-in {
        display: none;
    }

    &--sign-up {
        display: none;
    }

    &--restore-password {
        display: none;
    }

    &--success {
        display: none;
    }

    &--unsuccess {
        display: none;
    }

    &--fast-payment {
        padding: 0 16px;
        display: none;
    }

    &--quick-order {
        padding: 0 16px;
        display: none;
    }

    &--payment-success {
        display: none;
    }

    &--payment-unsuccess {
        display: none;
    }

    &--insufficient-funds {
        display: none;
    }
}

.popup__inner {
    padding: 30px;

    background-color: #F8FAFD;
    border-radius: 10px;

    position: relative;

    @media(max-width:767px) {
        padding: 15px 10px 20px;
    }

    &--sign-in {
        width: 454px;

        @media(max-width:1919px) {
            width: 385px;
        }

        @media(max-width:767px) {
            width: 328px;
        }
    }

    &--sign-up {
        width: 454px;

        @media(max-width:1919px) {
            width: 385px;
        }

        @media(max-width:767px) {
            width: 328px;
        }
    }

    &--restore-password {
        width: 454px;

        @media(max-width:1919px) {
            width: 385px;
        }

        @media(max-width:767px) {
            width: 328px;

            .form {
                display: flex;
                flex-direction: column;
            }

            .popup__title {
                margin-bottom: 10px;
            }

            .popup__block-sign-in {
                order: 4;
            }
        }
    }

    &--payment {
        width: 538px;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 15px 15px 20px;

        @media(max-width:1919px) {
            width: 340px;
            padding: 20px 11px;
        }
    }

    &--fast-payment {
        padding: 0;
        max-width: 914px;

        .popup__close {
            @media(max-width:767px) {
                width: 15px;
                height: 15px;
            }
        }

        .fast-payment {
            display: block;
            max-height: calc(var(--vh, 1vh) * 88);

            overflow-y: scroll;

            &::-webkit-scrollbar {
                display: none;
            }

            -ms-overflow-style: none;
            /* IE and Edge */
            scrollbar-width: none;
            /* Firefox */
        }

        .payment-method__list {
            justify-content: space-between;
        }

        .payment-method__item+.payment-method__item {
            margin-left: 0 !important;
        }
    }

    &--quick-order {
        max-height: calc(var(--vh, 1vh) * 88);
        max-width: 1170px;
        width: 100%;

        @media(max-width:1919px) {
         max-width: 900px;
        }

        @media(max-width:1023px) {
            max-width: 600px;
        }

        .quick-order {
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            flex-wrap: wrap;
            max-height: calc((var(--vh, 1vh) * 88) - 60px);
            margin: 0 -30px;
            padding: 0 30px;
            overflow-y: scroll;

            @media(max-width:767px) {
                margin: 0 -10px;
                padding: 0 10px;
            }

            &::-webkit-scrollbar {
                display: none;
            }

            -ms-overflow-style: none;
            /* IE and Edge */
            scrollbar-width: none;
            /* Firefox */
        }

        .order__menu-title {
            width: 100%;
            text-align: center;
        }

        .order__results-group {
            width: calc(50% - 15px);

            @media(max-width:1023px) {
                width: 100%;
            }

            @media(max-width:767px) {
                padding-left: 0;
                padding-right: 0;
            }
        }

        .quick-order__btn {
            margin-left: auto;
            margin-top: 30px;

            @media(max-width:767px) {
                margin-top: 15px;
                margin-right: auto;
            }
        }
    }

    &--payment-message {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 20px;
        width: 426px;

        @media(max-width:1439px) {
            width: 328px;
            padding: 15px;
        }
    }
}

.popup__close {
    width: 22px;
    height: 22px;

    position: absolute;
    right: 0;
    bottom: calc(100% + 15px);

    @media(max-width:767px) {
        bottom: calc(100% + 10px);
    }
}

.popup__title {
    margin-bottom: 20px;

    font-weight: 600;
    font-size: 30px;
    line-height: 41px;
    text-align: center;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 24px;
        line-height: 36px;
    }

    @media(max-width:767px) {
        font-size: 20px;
        line-height: 27px;
        margin-bottom: 10px;
    }
}


.forgot-password {
    font-weight: normal;
    font-size: 18px;
    line-height: 25px;
    color: #78A1CF;

    transition: .2s linear;

    @media(min-width:1024px) {
        &:hover {
            color: #3990F1;
        }
    }

    @media(max-width:1919px) {
        font-size: 16px;
        line-height: 22px;
    }

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 19px;
    }
}

.popup__btn-sign-in {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 15px;

    @media(max-width:767px) {
        margin-bottom: 20px;
    }
}

.popup__block-sign-up,
.popup__block-sign-in {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 25px;

    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    text-align: center;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 16px;
    }
}

.popup__block-sign-up {
    margin-bottom: 0;
}

.call__sign-up-popup,
.call__sign-in-popup {
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    text-align: center;
    color: #3990F1;

    display: inline-block;
    overflow: hidden;
    margin-left: 5px;

    position: relative;

    &::before {
        content: '';
        height: 1px;
        background-color: #3990F1;
        transition: .2s linear;

        position: absolute;
        left: 0;
        right: 0;
        bottom: 2px;

        @media(max-width:767px) {
            bottom: 0;
        }
    }

    @media(min-width:1024px) {
        &:hover::before {
            transform: translateX(100%);
        }
    }

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 16px;
    }
}

.popup__block-sign-in {
    margin-bottom: 0;
}

.popup__login-via-title {
    font-weight: normal;
    font-size: 30px;
    line-height: 41px;
    text-align: center;
    color: #545454;

    margin-bottom: 15px;

    @media(max-width:1919px) {
        font-size: 24px;
        line-height: 36px;
    }

    @media(max-width:767px) {
        font-weight: 600;
        font-size: 14px;
        line-height: 19px;
    }
}

.popup__login-via-block {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;

    @media(max-width:1919px) {
        margin-bottom: 10px;
    }
}

.popup__login-via-btn {
    width: 44px;
    height: 44px;
    margin: 0 12.5px;

    @media(max-width:1919px) {
        width: 40px;
        height: 40px;
        margin: 0 10px;
    }

    @media(max-width:767px) {
        width: 36px;
        height: 36px;
        margin: 0 10px;
    }
}

.popup__btn-sign-up {
    margin: 20px auto 20px;

    @media(max-width:767px) {
        margin-top: 15px;
        margin-bottom: 15px;
    }
}

.quick-registration {
    margin-bottom: 40px;

    @media(max-width:1919px) {
        margin-bottom: 30px;
    }

    @media(max-width:767px) {
        margin-bottom: 10px;
    }
}

.quick-registration__title {
    margin-bottom: 15px;

    font-weight: normal;
    font-size: 30px;
    line-height: 41px;
    text-align: center;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 24px;
        line-height: 36px;
    }

    @media(max-width:767px) {
        font-weight: 600;
        font-size: 14px;
        line-height: 19px;
    }
}

.quick-registration__block {
    display: flex;
    justify-content: center;
}

.quick-registration__btn {
    width: 44px;
    height: 44px;
    margin: 0 12.5px;

    @media(max-width:1919px) {
        width: 40px;
        height: 40px;
        margin: 0 10px;
    }

    @media(max-width:767px) {
        width: 36px;
        height: 36px;
    }
}

.popup__link {
    display: inline-block;
    overflow: hidden;

    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    text-align: center;
    color: #545454;

    position: relative;

    &::before {
        content: '';
        height: 1px;
        background-color: #545454;
        transition: .2s linear;

        position: absolute;
        left: 0;
        right: 0;
        bottom: 2px;

        @media(max-width:1023px) {
            bottom: 0;
        }
    }

    @media(min-width:1024px) {
        &:hover::before {
            transform: translateX(100%);
        }
    }

    @media(max-width:1023px) {
        font-size: 14px;
        line-height: 16px;
    }
}

.popup__descr {
    max-width: 345px;
    margin: 0 auto 20px;

    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    text-align: center;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 16px;
        margin-bottom: 10px;
        max-width: 270px;
    }
}

.popup__btn-submit {
    width: 186px;
    margin: 0 auto 20px;

    @media(max-width:767px) {
        order: 5;
        margin-bottom: 0;
        margin-top: 15px;
    }
}

.popup-payment__title {
    max-width: 462px;
    margin-bottom: 15px;

    font-weight: bold;
    font-size: 22px;
    line-height: 30px;
    text-align: center;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 16px;
        line-height: 22px;
        margin-bottom: 10px;
    }

    span {
        font-weight: 400;
        font-size: 22px;
        line-height: 30px;
        text-align: center;
        color: #545454;

        @media(max-width:1919px) {
            font-size: 16px;
            line-height: 22px;
        }
    }
}

.popup-payment__icon {
    width: 65px;
    height: 65px;
    margin-bottom: 15px;

    @media(max-width:1919px) {
        width: 39px;
        height: 39px;
    }
}

.popup-payment__text {
    max-width: 356px;
    margin-bottom: 9px;

    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    text-align: center;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 16px;

        max-width: 253px;
        margin-bottom: 15px;
    }

    &--unsuccess {
        max-width: 284px;

        @media(max-width:1919px) {
            max-width: 213px;
        }
    }
}

.payment-message__title {
    font-weight: bold;
    font-size: 22px;
    line-height: 30px;
    color: #545454;

    margin-bottom: 10px;
    padding-left: 32px;

    position: relative;

    @media(max-width:1919px) {
        font-size: 18px;
        line-height: 26px;
    }

    @media(max-width:1439px) {
        font-size: 16px;
        line-height: 22px;

        padding-left: 26px;
    }

    &::before {
        content: '';
        width: 22px;
        height: 22px;

        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);

        @media(max-width:1439px) {
            width: 18px;
            height: 18px;

            background-size: cover;
        }
    }

    &--success::before {
        background-image: url("../img/like-green.svg");
    }

    &--unsuccess::before {
        background-image: url("../img/warning.svg");
    }
}

.payment-message__text {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    text-align: center;
    color: #545454;

    margin-bottom: 20px;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:1439px) {
        margin-bottom: 10px;

        line-height: 16px;
    }

    span {
        color: inherit;
        font-size: inherit;
        font-weight: inherit;
        line-height: inherit;
    }

    &--insufficient-funds {
        max-width: 293px;

        @media(max-width:767px) {
            max-width: 268px;
        }
    }
}

.payment-message__btns {
    width: 294px;
    display: flex;
    justify-content: space-between;
}


.payment-message__btn {
    width: 132px;
    height: 49px;

    @media(max-width:1439px) {
        width: 135px;
        height: 42px;
    }
}