.prompt {
    background: #F8FAFD;
    border-radius: 10px;
    pointer-events: none;
    opacity: 0;
    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.15);

    transition: .2s linear;

    font-weight: normal;
    font-size: 14px;
    line-height: 19px;
    color: #545454;
    text-align: start;

    position: absolute;
    bottom: calc(100% + 13px);
    left: 50px;

    &::before {
        content: '';
        width: 0;
        height: 0;

        border-style: solid;
        border-width: 11px 7px 0 7px;
        border-color: #F8FAFD transparent transparent transparent;

        position: absolute;
        top: calc(100% - 2px);
        left: 16px;
    }

    &--continue {
        width: 317px;
        padding: 10px;

        @media(max-width:1500px) {
            left: 25px;
        }
    }

    &--quick-order {
        width: 317px;
        padding: 10px;

        @media(max-width:1500px) {
            left: unset;
            right: 25px;

            &::before {
                left: unset;
                right: 16px;
            }
        }
    }

    &--order {
        font-weight: 600;
        font-size: 16px;
        line-height: 22px;
        color: #545454;
        white-space: nowrap;
        text-transform: capitalize;

        padding: 12px 44px;

        left: 0;
        top: 0;
        bottom: unset;
        z-index: 1;
        transition: opacity .2s linear;

        &::before {
            top: calc(100% - 4px);
            left: 21px;
        }

        &.active {
            opacity: 1;
        }

        @media(max-width:1919px) {
            font-size: 14px;
            line-height: 18px;
            padding: 12px 30px;
        }
    }
}