.error-page {
    padding-top: 154px;
    margin-bottom: 146px;

    @media(max-width:1919px) {
        margin-bottom: 40px;
    }

    @media(max-width:1023px) {
        padding-top: 95px;
    }

    @media(max-width:767px) {
        margin-bottom: 237px;
    }
}


.error-page__title {
    font-weight: 800;
    font-size: 82px;
    line-height: 112px;
    text-transform: uppercase;
    color: #FFFFFF;

    margin-bottom: 5px;

    @media(max-width:1919px) {
        font-size: 60px;
        line-height: 90px;
    }

    @media(max-width:1439px) {
        font-size: 50px;
        line-height: 70px;
    }

    @media(max-width:767px) {
        font-size: 40px;
        line-height: 54px;
    }
}

.error-page__subtitle {
    font-weight: normal;
    font-size: 22px;
    line-height: 30px;
    color: #FFFFFF;

    margin-bottom: 29px;

    @media(max-width:1919px) {
        font-size: 21px;
        line-height: 28px;
    }

    @media(max-width:1439px) {
        font-size: 19px;
        line-height: 26px;
        margin-bottom: 12px;
    }
}

.error-page__btn {
    fill: #3990F1;

    svg {
        margin-right: 6px;
        width: 10px;
        height: 10px;
    }

    @media(min-width:1024px) {
        &:hover {
            fill: #fff;
        }
    }
}

.error-page__photo {
    width: 1375px;
    height: 739px;

    pointer-events: none;
    position: absolute;
    top: 218px;
    right: 0;

    @media(max-width:1919px) {
        width: 60vw;
        height: auto;
    }

    @media(max-width:1439px) {
        top: 327px;
    }

    @media(max-width:1023px) {
        top: 260px;
        width: 70vw;
    }

    @media(max-width:767px) {
        top: 255px;
        right: 0;
        left: 0;
        width: auto;
        // transform: translateX(-50%);
    }

    @media(max-width:525px) {
        top: 325px;
        height: 320px;
    }

    @media(max-width:375px) {
        top: 255px;
        width: 100%;
        height: 311px;
    }

    @media(max-width:320px) {
        top: 340px;
    }

}