.order__inner {
    @media(max-width:1919px) {
        max-width: 1000px;
        margin: 0 auto;
    }
}

.order__title {
    margin-bottom: 60px;

    @media(max-width:1919px) {
        margin-bottom: 40px;
    }

    @media(max-width:767px) {
        margin-bottom: 30px;
    }
}

.order-wrapper {
    @media(max-width:767px) {
        width: 100%;
        margin: 0 auto;
    }
}

.order__box {
    width: 100%;
    padding-left: 33px;
    margin-bottom: 30px;
    height: 68px;

    background: #F8FAFD;
    border-radius: 10px;
    // overflow: visible;

    position: relative;

    &--white {
        background-color: #fff;

        @media(max-width:1023px) {

            .order-input,
            .order__list {
                background-color: #fff;
            }
        }
    }

    @media(max-width:1919px) {
        height: 50px;
        margin-bottom: 15px;
    }

    @media(max-width:1023px) {
        height: auto;
        padding-left: 0;
        margin-bottom: 20px;
        overflow: visible;
        z-index: 10;

        &::before {
            content: '';
            width: 9px;
            height: 11px;


            background-image: url("../img/triangle.svg");
            background-repeat: no-repeat;
            background-size: 100% 100%;
            pointer-events: none;

            position: absolute;
            top: 19px;
            right: 15px;

            transition: .3s linear;
        }

        &.active::before {
            transform: rotate(180deg);
        }
    }

    @media(max-width:767px) {
        &::before {
            width: 6px;
            height: 8px;
            top: 17px;
        }
    }
}

.order__img {
    display: none;

    @media(max-width:1023px) {
        display: block;
        max-width: 20px;
        height: auto;

        position: absolute;
        left: 15px;
        top: 14px;
    }

    @media(max-width:767px) {
        max-width: 15px;
        left: 15px;
        top: 12px;
    }
}

.order-input {
    display: none;


    @media(max-width:1023px) {
        display: flex;
        align-items: center;
        width: 100%;
        height: 47px;

        cursor: pointer;
        border: none;
        border-radius: 10px;
        padding: 11px 15px 11px 45px;
        background-color: #F8FAFD;

        font-weight: normal;
        font-size: 14px;
        line-height: 16px;
        color: #545454;
        text-transform: capitalize;

        &::placeholder {
            font-weight: normal;
            font-size: 14px;
            line-height: 16px;
            color: #545454;
            text-transform: capitalize;
        }

        &--services {
            padding-left: 15px;
        }

        &--services2 {
            padding-left: 15px;
        }
    }

    @media(max-width:767px) {
        height: 39px;

        &--social {
            padding-left: 35px;
        }
    }
}

.order__list {
    @media(max-width:1023px) {
        display: flex;
        flex-direction: column;
        padding: 0;
        height: auto;
        max-height: 0;

        background-color: #F8FAFD;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 10px;

        position: absolute;
        top: calc(100% + 4px);
        left: 0;
        right: 0;
        z-index: 10;

        transition: .3s linear;

        &.active {
            max-height: 187px;
            padding: 7.5px 0;
            z-index: 20;
        }

        overflow: scroll;

        &::-webkit-scrollbar {
            display: none;
        }

        -ms-overflow-style: none;
        scrollbar-width: none;
    }
}

.order__item {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 108px;
    height: 68px;


    position: relative;
    transition: .2s linear;
    cursor: pointer;

    &::before {
        content: '';
        height: 28px;
        width: 1px;

        background-color: #EBEFF4;

        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
    }

    img {
        transition: .2s linear;
        width: unset;
        height: unset;

        @media(max-width:1919px) {
            max-width: 28px;
            height: auto;
        }
    }

    &.active {
        background-color: #EBF0F8;

        @media(max-width:767px) {
            @media(max-width:767px) {
                .order__name {
                    font-weight: 600;
                    color: #000;
                }
            }
        }
    }

    @media(min-width:1024px) {
        &:hover:not(.active) {
            img {
                transform: scale(1.1);
            }
        }

        &:hover {
            .prompt {
                opacity: 1;
            }
        }
    }

    @media(max-width:1919px) {
        width: 90px;
        height: 50px;
    }

    @media(max-width:1023px) {
        height: auto;
        width: 100%;
        justify-content: flex-start;
        padding:12px 15px;

        img {
            max-width: 15px;
        }
    }
}

.order__name {
    display: none;

    @media(max-width:1023px) {
        display: block;
        margin-left: 5px;

        font-weight: normal;
        font-size: 14px;
        line-height: 18px;
        color: #545454;
        text-transform: capitalize;
    }
}

.order__btn-left {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 2;

    width: 41px;

    background: rgba(197, 213, 239, 0.7);
    border-radius: 10px 0px 0px 10px;

    &::before {
        content: '';
        position: absolute;
        top: 25px;
        left: 12px;

        transition: .2s linear;

        width: 0;
        height: 0;
        border-style: solid;
        border-width: 9.5px 15px 9.5px 0;
        border-color: transparent rgba(#3990F1, .5) transparent transparent;

        @media(max-width:1919px) {
            top: 19px;
            left: 10px;
            border-width: 7.5px 12px 7.5px 0;
        }
    }

    @media(max-width:1919px) {
        width: 33px;
    }

    @media(max-width:1023px) {
        display: none;
    }
}

.order__btn-right {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;

    width: 41px;

    background: rgba(197, 213, 239, 0.7);
    border-radius: 0px 10px 10px 0px;
    z-index: 2;

    &::before {
        content: '';
        position: absolute;
        top: 25px;
        right: 12px;

        transition: .2s linear;

        width: 0;
        height: 0;
        border-style: solid;
        border-width: 9.5px 15px 9.5px 0;
        border-color: transparent rgba(#3990F1, .5) transparent transparent;

        transform: rotate(180deg);

        @media(max-width:1919px) {
            top: 19px;
            right: 10px;
            border-width: 7.5px 12px 7.5px 0;
        }
    }


    @media(max-width:1919px) {
        width: 33px;
    }

    @media(max-width:1023px) {
        display: none;
    }
}

.order__btn-left,
.order__btn-right {
    transition: .2s linear;

    @media(min-width:1024px) {
        &:hover {
            background-color: #3D92F2;

            &::before {
                border-color: transparent #fff transparent transparent;

            }
        }
    }
}

.order__menu {
    display: flex;
    justify-content: space-between;
    height: auto;

    &.hide {
        display: none;
    }

    &.show {
        animation: fade .7s linear;
    }

    @media(max-width:1023px) {
        flex-direction: column;
        align-items: center;
    }
}

.order__services {
    width: calc(50% - 15px);
    padding-top: 30px;

    background-color: #F8FAFD;
    border-radius: 10px;

    @media(max-width:1919px) {
        width: calc(50% - 7.5px);
        padding-top: 15px;
    }

    @media(max-width:1023px) {
        width: 100%;
        margin-bottom: 20px;
        padding-top: 0;
        border-radius: 0;
        background-color: transparent;

        position: relative;

        &::before {
            content: '';
            width: 9px;
            height: 11px;

            background-image: url("../img/triangle.svg");
            background-repeat: no-repeat;
            background-size: 100% 100%;
            pointer-events: none;

            position: absolute;
            right: 15px;
            top: 58px;
            transition: .3s linear;
        }

        &.active::before {
            transform: rotate(180deg);
        }
    }

    @media(max-width:767px) {
        &::before {
            width: 6px;
            height: 8px;
            top: 50px;
        }
    }

    &--white {
        background-color: #FFFFFF;

        @media(max-width:1023px) {
            background-color: transparent;

            .order-input,
            .order__services-list {
                background-color: #fff;
            }
        }
    }
}

.order__menu-title {
    font-weight: 600;
    font-size: 22px;
    line-height: 30px;
    text-align: center;
    color: #545454;

    margin-bottom: 30px;

    @media(max-width:1919px) {
        font-size: 18px;
        line-height: 26px;
        margin-bottom: 20px;
    }

    @media(max-width:1023px) {
        margin-bottom: 15px;
        font-weight: bold;
        text-align: start;
        font-size: 16px;
        line-height: 24px;
    }

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 19px;
    }

    &--mob {
        display: none;

        @media(max-width:1023px) {
            display: block;
        }
    }
}

.order__services-list {
    display: flex;
    flex-direction: column;

    @media(max-width:1023px) {
        display: flex;
        flex-direction: column;
        padding: 0;
        height: auto;
        max-height: 0;

        background-color: #F8FAFD;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 10px;

        position: absolute;
        top: calc(100% + 4px);
        left: 0;
        right: 0;
        z-index: 10;

        transition: .3s linear;

        &.active {
            max-height: 187px;
            padding: 7.5px 0;
            z-index: 20;
        }

        overflow: scroll;

        &::-webkit-scrollbar {
            display: none;
        }

        -ms-overflow-style: none;
        scrollbar-width: none;
    }
}

.order__services-item {
    height: 58px;
    display: flex;
    align-items: center;
    padding-left: 78px;

    cursor: pointer;

    position: relative;
    z-index: 2;
    transition: .2s linear;
    overflow: hidden;

    &::before {
        content: '';
        width: 4px;
        height: 9px;

        background-image: url("../img/arrow-grey.svg");
        background-size: 100% 100%;

        position: absolute;
        right: 25px;
        top: 50%;
        transform: translateY(-50%);

        @media(max-width:1023px) {
            display: none;
        }
    }

    &::after {
        content: '';
        background-color: #EBF0F8;

        transform: translateX(-100%);
        transition: .2s linear;

        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: -1;

        @media(max-width:1023px) {
            transform: translateX(0);
            opacity: 0;
        }
    }

    &.active {
        &::after {
            transform: translateX(0);

            @media(max-width:1023px) {
                opacity: 1;
            }
        }

        &::before {
            background-image: url("../img/arrow-blue.svg");
        }

        @media(max-width:767px) {
            .order__services-name {
                font-weight: 600;
                color: #000;
            }
        }
    }

    @media(max-width:1919px) {
        height: 48px;
        padding-left: 50px;
    }

    @media(max-width:1599px) {
        height: 44px;
    }

    @media(max-width:1023px) {
        min-height: 42px;
        width: 100%;
        justify-content: flex-start;
        padding: 0 15px;
    }

    @media(min-width:1024px) {
        &:hover:not(.active) {
            opacity: .7;
        }
    }
}

.order__services-img {
    max-width: 28px;

    position: absolute;
    left: 30px;
    top: 50%;
    transform: translateY(-50%);

    img {
        object-fit: contain;
    }

    @media(max-width:1919px) {
        max-width: 20px;
        left: 14px;
    }

    @media(max-width:1023px) {
        display: none;
    }
}

.order__services-name {
    font-weight: 600;
    font-size: 18px;
    line-height: 25px;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 16px;
        line-height: 23px;
    }

    @media(max-width:1599px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:1023px) {
        font-weight: normal;
        color: #545454;
        text-transform: capitalize;
    }
}


.order__results {
    width: calc(50% - 15px);
    padding: 30px;

    background-color: #F8FAFD;
    border-radius: 10px;

    @media(max-width:1919px) {
        width: calc(50% - 7.5px);
        padding: 15px;
    }

    @media(max-width:1023px) {
        width: 100%;
    }

    @media(max-width:767px) {
        padding: 0;
        background-color: transparent;

        position: relative;

        &::before {
            content: '';
            background-color: #F8FAFD;
            border-radius: 10px;

            position: absolute;
            top: 34px;
            left: 0;
            right: 0;
            bottom: 0;
        }
    }

    &--white {
        background-color: #fff;

        @media(max-width:767px) {
            background-color: transparent;
        }
    }
}

.order__results-group {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 20px;

    position: relative;

    @media(max-width:767px) {
        padding: 10px 10px 0;
        margin-bottom: 5px;
    }

    &.error {
        .error__text {
            display: block;
        }

        .input {
            border: 1px solid #FF4848;
        }
    }
}

.order__results-title {
    font-weight: bold;
    font-size: 18px;
    line-height: 25px;
    color: #545454;

    margin-bottom: 15px;

    @media(max-width:1919px) {
        font-size: 16px;
        line-height: 23px;
    }

    @media(max-width:1023px) {
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 18px;

        margin-bottom: 10px;
    }
}

.order-promo-code__activate {
    height: 57px;
    display: flex;
    align-items: center;
    padding-right: 15px;

    font-weight: 600;
    font-size: 18px;
    line-height: 25px;
    color: #78A1CF;

    transition: .2s linear;

    position: absolute;
    bottom: 0;
    right: 0;

    @media(min-width:1024px) {
        &:hover {
            color: #1A6DCA;
        }
    }

    @media(max-width:1919px) {
        height: 47px;
        font-size: 16px;
        line-height: 23px;
    }

    @media(max-width:1599px) {
        font-size: 14px;
        line-height: 16px;
    }

    @media(max-width:1023px) {
        padding-right: 20px;
    }

    @media(max-width:767px) {
        height: 39px;
    }
}

.order__results-btns {
    display: flex;
    justify-content: space-between;

    @media(max-width:767px) {
        padding: 0 10px;
        margin-bottom: 10px;
        margin-top: 15px;
    }
}

.order__discount {
    display: flex;
    align-items: center;
    z-index: 2;
}

.order__discount-icon {
    display: block;
    width: 38px;
    height: 38px;
    margin-right: 22px;

    @media(max-width:1919px) {
        width: 29px;
        height: 29px;
        margin-right: 12px;
    }
}

.order__discount-text {
    max-width: 122px;

    font-weight: 600;
    font-size: 18px;
    line-height: 25px;
    color: #78A1CF;

    @media(max-width:1919px) {
        max-width: 98px;
        
        font-size: 14px;
        line-height: 16px;
        color: #78A1CF;
    }
}

.order__results-continue,
.quick-order__btn {
    position: relative;

    @media(min-width:1024px) {
        &:hover {
            .prompt {
                opacity: 1;
            }
        }
    }
}

.order__results-btn {
    margin-bottom: 15px;

    font-weight: 600;
    font-size: 18px;
    line-height: 25px;
    color: #78A1CF;

    transition: .2s linear;

    @media(min-width:1024px) {
        &:hover {
            color: #1A6DCA;
        }
    }

    @media(max-width:1919px) {
        font-size: 16px;
        line-height: 23px;
    }

    @media(max-width:1023px) {
        font-weight: 600;
        font-size: 14px;
        line-height: 18px;

        margin-bottom: 10px;
    }
}

.order__range-box {
    position: relative;
    margin-bottom: 37px;

    @media(max-width:1023px) {
        margin-bottom: 35px;

        &:nth-child(2) {
            margin-right: 54px;
        }
    }
}

.order__range-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 20px;
    height: 20px;

    position: absolute;
    bottom: 19.5px;

    @media(max-width:1919px) {
        bottom: 13.5px;
    }

    @media(max-width:767px) {
        bottom: 9.5px;
    }

    img {
        width: 10px;
        height: auto;
    }

    &--minus {
        left: 10px;

        @media(max-width:767px) {
            left: 8px;
        }
    }

    &--plus {
        right: 10px;

        @media(max-width:767px) {
            right: 8px;
        }
    }
}

.order__results-value {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 26px;
    height: 57px;

    background: #FFFFFF;
    border-radius: 10px;

    font-family: Open Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 25px;
    color: #545454;

    &--grey {
        background: #F8FAFD;

        @media(max-width:767px) {
            background: #fff;
        }
    }

    @media(max-width:1919px) {
        font-size: 16px;
        line-height: 23px;
        height: 47px;
    }

    @media(max-width:1599px) {
        font-size: 14px;
        line-height: 18px;
        padding: 0 10px;
    }

    @media(max-width:767px) {
        height: 39px;
    }
}

.swiper-slide-invisible-blank {
    display: none;
}