.intro {
    height: 955.5px;

    background-image: url('../img/intro-bg.svg');
    background-size: cover;
    background-position: top;
    background-repeat: no-repeat;

    @media(max-width:1919px) {
        height: 800px;
    }

    @media(max-width:1599px) {
        height: 660px;
    }

    @media(max-width:1023px) {
        height: 624px;
    }

    @media(max-width:767px) {
        // height: 491px;
        margin-bottom: 18px;
        background-image: url('../img/intro-bg-mob2.svg');
    }

    @media(max-width:375px) {
        background-image: url('../img/intro-bg-mob.svg');
    }
}

.intro__inner {
    padding-top: 220px;

    position: relative;

    @media(max-width:1599px) {
        padding-top: 170px;
    }

    @media(max-width:1023px) {
        padding-top: 134px;
    }

    @media(max-width:767px) {
        padding-top: 150px;
    }
}

.intro__title {
    font-weight: 800;
    font-size: 50px;
    line-height: 68px;

    margin-bottom: 31px;
    max-width: 620px;


    @media(max-width:1919px) {
        font-size: 40px;
        line-height: 48px;
    }

    @media(max-width:1599px) {
        font-size: 36px;
        line-height: 44px;
        margin-bottom: 15px;
        max-width: 440px;
    }

    @media(max-width:1439px) {
        font-size: 34px;
        line-height: 36px;
    }

    @media(max-width:767px) {
        font-size: 26px;
        line-height: 35px;
        max-width: 360px;
        margin-bottom: 15px;
    }
}

.intro__social {
    width: 520.67px;
    height: 44px;
    margin-bottom: 14px;
    background-image: url("../img/intro-social.svg");
    background-size: contain;
    background-repeat: no-repeat;

    @media(max-width:1439px) {
        width: 480.67px;
    }

    @media(max-width:767px) {
        background-image: url("../img/intro-social-mob.svg");
        width: 290px;
        height: 28.48px;
        margin-bottom: 16px;
    }
}

.intro__subtitle {
    max-width: 474px;
    margin-bottom: 35px;

    @media(max-width:767px) {
        width: 328px;
        margin-bottom: 22px;
        font-size: 16px;
        line-height: 22px;
    }
}

.intro__nav {
    width: 343px;
    display: flex;
    justify-content: space-between;

    @media(max-width:1919px) {
        width: 305px;
    }

    @media(max-width:1023px) {
        width: 287px;
        margin-bottom: 36px;
    }
}

.intro__illustration {
    display: block;
    width: 764.9px;
    height: 622.04px;

    position: absolute;
    top: 154.44px;
    right: -237px;

    @media(max-width:1919px) {
        width: 600px;
        height: 470px;

        right: -60px;
    }

    @media(max-width:1599px) {
        width: 525px;
        height: 440px;
    }

    @media(max-width:1439px) {
        width: 485px;
        height: 400px;
        top: 121.44px;
        right: -30px;
    }

    /* 
    @media(max-width:1023px) {
        position: relative;
        top: 0%;
        right: 0;

        margin: 0 auto;
    } */


    @media(max-width:1023px) {
        display: none;
    }

    /*     @media(max-width:850px) {
        width: 474.9px;
        height: 332.04px;
    }

    @media(max-width:767px) {
        width: 255.53px;
        height: 234.49px;
    } */
}

.intro__illustration-main-screen {
    width: 620.99px;
    height: 387.96px;

    position: absolute;
    top: 61px;
    left: 48px;

    @media(max-width:1919px) {
        width: 491.99px;
        height: 325.96px;
        top: 27px;
        left: 37px;

        img {
            object-position: center;
            object-fit: contain;
        }
    }

    @media(max-width:1599px) {
        width: 440px;
        height: 320.96px;
        top: 19px;
        left: 26px;
    }

    @media(max-width:1439px) {
        width: 400px;
        top: 2px;
        height: 323.96px;
        left: 28px;
    }
}

.intro__illustration-item {
    width: 623.42px;
    height: 386.17px;

    position: absolute;
    top: 63px;
    left: 47px;
    z-index: 2;

    opacity: 0;
    transition: transform .5s linear;


    @media(max-width:1919px) {
        width: 496.42px;
        height: 334.17px;
        top: 24px;
        left: 36px;

        img {
            object-position: center;
            object-fit: contain;
        }
    }

    @media(max-width:1599px) {
        width: 442.42px;
        height: 310.17px;
        top: 24px;
        left: 26px;
    }

    @media(max-width:1439px) {
        width: 401.42px;
        top: 8px;
    }

    @media(max-width:1023px) {
        display: none;
    }

    /* 
    @media(max-width:1023px) {
        pointer-events: none;
    }

    @media(max-width:850px) {
        width: 427.42px;
        height: 240.17px;
        top: 11px;
        left: 9px;
    }

    @media(max-width:767px) {
        width: 264.42px;
        height: 146.17px;
        top: 24px;
        left: -15px;
    } */

    &.step1 {
        transform: translate(-67px, -67px);
        opacity: 1;

        @media(max-width:1919px) {
            transform: translate(-47px, -47px);
        }

        /* 
        @media(max-width:850px) {
            transform: translate(-37px, -37px);
        }

        @media(max-width:767px) {
            transform: translate(-20px, -20px);
        } */
    }

    &.step2 {
        transition: .3s linear;
        transform: translate(-220px, -97px) !important;
        opacity: 0;
        /* 
        @media(max-width:1023px) {
            transform: translate(-150px, -67px) !important;
        }

        @media(max-width:767px) {
            transform: translate(-70px, -40px);
        } */
    }

    &:last-child {
        z-index: 1;

        &.step1 {
            transform: translate(-27px, -27px);
            /* 
            @media(max-width:850px) {
                transform: translate(-17px, -17px);
            }

            @media(max-width:767px) {
                transform: translate(-10px, -10px);
            } */
        }
    }
}