.input {
    display: block;
    width: 100%;
    height: 57px;
    padding: 15px 16px;

    background-color: #FFFFFF;
    border-radius: 10px;
    border: none;

    font-weight: 400;
    font-size: 18px;
    line-height: 25px;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 16px;
        line-height: 23px;

        height: 47px;
    }

    @media(max-width:1599px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:1023px) {
        padding: 11px 10px;
    }

    @media(max-width:767px) {
        height: 39px;
    }

    &::placeholder {
        font-weight: 400;
        color: #D5D5D5;
    }

    &--settings::placeholder {
        color: #D5D5D5;
    }

    &--grey {
        background: #F8FAFD;

        @media(max-width:767px) {
            background: #fff;
        }
    }
}

.checkbox {
    display: flex;
    justify-content: center;

    &--popup {
        justify-content: flex-start;
    }
}

.checkbox__input {
    display: none;

    &:checked+.checkbox__fake {
        border-color: transparent;

        &::before {
            opacity: 1;
        }
    }
}

.checkbox__fake {
    min-width: 18px;
    width: 18px;
    height: 18px;

    border: 1px solid #3990F1;
    border-radius: 3px;

    transition: .2s linear;
    position: relative;

    @media(max-width:767px) {
        min-width: 14px;
        width: 14px;
        height: 14px;
    }

    &::before {
        content: '';
        width: 18px;
        height: 18px;
        background-image: url('../img/checkbox.svg');
        opacity: 0;

        transition: .2s linear;

        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        @media(max-width:767px) {
            width: 14px;
            height: 14px;
            background-size: cover;
        }
    }

    &--black {
        border: 1px solid #696969;
    }
}

.checkbox__text {
    max-width: 317px;
    margin-left: 10px;
    margin-top: -2px;

    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #545454;

    @media(max-width:1023px) {
        font-size: 14px;
        line-height: 16px;
    }
}

.error__text {
    display: none;
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #FF4848;

    margin-bottom: 15px;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 14px;
        margin-bottom: 10px;
    }
}