.payment-method {
    .payment-method__list {
        @media(max-width:1023px) {
            flex-direction: column;
            align-items: flex-start;
            margin-left: 10px;
        }
    }

    .payment-method__content {
        @media(max-width:1023px) {
            width: 308px;
        }

        @media(max-width:767px) {
            &::before {
                height: 60px;
                right: unset;
                width: 308px;
                opacity: 1;
                background-color: #FFFFFF;
            }
        }
    }

    .payment-method__item+.payment-method__item {
        @media(max-width:1023px) {
            margin-left: 0;
            margin-top: 30px;
        }
    }

    .payment-method__icon {
        @media(max-width:1023px) {
            max-width: 40px;
            height: auto;
            margin-right: 16px;
        }
    }

    .payment-method__name {
        @media(max-width:1919px) {
            font-size: 14px;
            line-height: 19px;
            margin-bottom: 7px;
        }
    }

    .payment-method__commission {
        @media(max-width:1919px) {
            font-size: 14px;
            line-height: 14px;
        }
    }
}

.payment-method__list {
    display: flex;
    align-items: center;
    margin-bottom: 40px;
}

.payment-method__item {
    display: flex;
    cursor: pointer;

    &+& {
        margin-left: 60px;
    }

    @media(min-width:1024px) {
        &:hover {
            .payment-method__name {
                // font-weight: 700;
                color: #3990F1;
            }

            .payment-method__commission {
                color: #78A1CF;
            }
        }
    }
}

.payment-method__input {
    display: none;

    &:checked+.payment-method__content {
        &::before {
            background: #EBF0F8;
            opacity: 1;
        }
    }
}

.payment-method__content {
    display: flex;
    align-items: center;

    position: relative;
    z-index: 2;

    &::before {
        content: '';
        height: 75px;

        background: #EBF0F8;
        border-radius: 10px;
        opacity: 0;
        transition: .2s linear;

        position: absolute;
        top: 50%;
        left: -10px;
        right: -10px;
        z-index: -1;
        transform: translateY(-50%);

        @media(max-width:1919px) {
            height: 65px;
        }
    }
}

.payment-method__icon {
    margin-right: 15px;

    img {
        width: unset;
        height: unset;
    }
}


.payment-method__name {
    margin-bottom: 4px;

    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    color: #545454;

    transition: .2s linear;
}

.payment-method__commission {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #696969;

    transition: .2s linear;
}

.payment-method__info {
    display: flex;
    margin-bottom: 40px;

    @media(max-width:1023px) {
        flex-direction: column;
        margin-bottom: 30px;
    }
}

.payment-method__info-title {
    margin-bottom: 15px;

    font-weight: bold;
    font-size: 18px;
    line-height: 25px;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 16px;
        line-height: 22px;
        margin-bottom: 20px;
    }
}

.payment-method__info-descr {
    max-width: 361px;

    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #F19C39;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 14px;

        max-width: 175px;
        margin-left: 13px;
    }
}

.payment-method__select {
    width: 394px;
    margin-bottom: 30px;

    @media(max-width:767px) {
        width: 308px;
    }
}

.payment-method__btn-block {
    @media(max-width:767px) {
        display: flex;
    }
}

.payment-method__btn {
    margin-bottom: 20px;
}

.get-more {
    margin-left: 176px;

    @media(max-width:1023px) {
        margin-left: 0;
        margin-top: 25px;
    }

    @media(max-width:767px) {
        margin-top: 30px;
    }
}

.get-more__title {
    margin-bottom: 15px;

    font-weight: bold;
    font-size: 18px;
    line-height: 25px;
    color: #78A1CF;

    @media(max-width:1919px) {
        font-size: 16px;
        line-height: 22px;
    }

    @media(max-width:767px) {
        text-align: center;
    }
}

.get-more__descr {
    max-width: 263px;
    margin-bottom: 25px;

    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
        margin-bottom: 15px;
    }

    @media(max-width:767px) {
        line-height: 16px;
        text-align: center;
        margin-left: auto;
        margin-right: auto;
        max-width: 230px;
    }
}

.get-more__bonus {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:767px) {
        line-height: 16px;
        text-align: center;
    }

    span {
        font-weight: normal;
        font-size: 16px;
        line-height: 22px;
        color: #78A1CF;


        @media(max-width:1919px) {
            font-size: 14px;
            line-height: 18px;
        }

        @media(max-width:767px) {
            line-height: 16px;
        }
    }
}

.balance-history {
    margin-bottom: 30px;

    @media(max-width:767px) {
        margin-bottom: 20px;
    }
}

.balance-history__title {
    font-weight: 600;
    font-size: 22px;
    line-height: 30px;
    color: #545454;

    margin-bottom: 30px;

    @media(max-width:1919px) {
        font-size: 18px;
        line-height: 26px;
    }

    @media(max-width:767px) {
        font-size: 16px;
        line-height: 22px;
        margin-bottom: 20px;
    }
}

.balance-history__board {
    background-color: #FFFFFF;
    border-radius: 10px;
    padding-bottom: 15px;

    @media(max-width:767px) {
        padding-bottom: 10px;
    }
}

.balance-history__top {
    display: flex;
    align-items: center;
    padding: 12px 20px 13px 17px;
    background-color: #EBF0F8;
    border-radius: 10px 10px 0px 0px;

    @media(max-width:767px) {
        padding: 5px 8px;
    }

    .balance-history__column {
        font-weight: 600;
        font-size: 16px;
        line-height: 22px;
        color: #545454;

        @media(max-width:1919px) {
            font-size: 14px;
            line-height: 18px;
        }

        @media(max-width:767px) {
            font-size: 12px;
            line-height: 16px;
        }
    }
}

.balance-history__column {
    width: 391px;

    &:last-child {
        width: 120px;
        margin-left: auto;

        @media(max-width:1023px) {
            width: auto;
            min-width: 120px;
        }

        @media(max-width:767px) {

            min-width: 70px;
        }
    }

    @media(max-width:1919px) {
        width: 30%;
    }

    @media(max-width:767px) {
        width: 35%;
    }
}

.balance-history__content {
    margin: 15px 10px 0 17px;
    height: 148px;

    @media(max-width:767px) {
        margin: 10px 5px 0 5px;
    }

    @include list-scroll;

    .balance-history__column {
        font-weight: normal;
        font-size: 16px;
        line-height: 22px;
        color: #545454;

        @media(max-width:1919px) {
            font-size: 14px;
            line-height: 18px;
        }

        @media(max-width:767px) {
            font-size: 10px;
            line-height: 14px;
        }
    }
}

.balance-history__row {
    display: flex;
    align-items: center;

    &+& {
        margin-top: 15px;

        @media(max-width:767px) {
            margin-top: 10px;
        }
    }
}

.chat {
    display: flex;
    align-items: center;
    padding-left: 38px;

    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #696969;

    position: relative;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:767px) {
        line-height: 16px;
        display: block;
    }

    &::before {
        content: '';
        width: 23px;
        height: 23px;

        background-image: url("../img/support.svg");

        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
    }
}

.chat__link {
    display: block;
    margin-left: 5px;

    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #78A1CF;

    overflow: hidden;
    position: relative;


    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:767px) {
        line-height: 16px;
        display: inline;
        margin-left: 0;
        text-decoration: underline !important;
    }

    &::before {
        content: '';
        height: 1px;
        background-color: #78A1CF;

        transition: .2s linear;

        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;

        @media(max-width:767px) {
            display: none;
        }
    }

    @media(min-width:1024px) {
        &:hover::before {
            transform: translateX(110%);
        }
    }
}