.range-slider {
    width: 100%;

    @media(max-width:1919px) {
        width: 95%;
        margin: 0 auto;
    }

    @media(max-width:767px) {
        margin: 0 10px;
    }
}

.js-input {
    max-width: 192px;

    font-family: Open Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 25px;
    text-align: center;
    color: #545454;

    @media(max-width:1919px) {
        max-width: 172px;
        font-size: 16px;
        line-height: 23px;
    }

    @media(max-width:1599px) {
        max-width: 148px;
        font-size: 14px;
        line-height: 18px;
    }
}

.irs--round {
    height: 1px;
}

.irs--round .irs-line {
    height: 1px;
    background: #545454;
    top: 0;
    overflow: visible;

    &::before {
        content: '';
        width: 9px;
        height: 9px;

        background: #545454;
        border-radius: 50%;

        position: absolute;
        top: 50%;
        left: -4px;
        transform: translateY(-50%);
    }

    &::after {
        content: '';
        width: 9px;
        height: 9px;

        background: #545454;
        border-radius: 50%;

        position: absolute;
        top: 50%;
        right: -4px;
        transform: translateY(-50%);
    }
}

.irs--round .irs-bar {
    height: 1px;
    background: #545454;
    top: 0;
}

.irs--round .irs-handle {
    top: 50%;
    transform: translateY(-50%);

    width: 27px;
    height: 27px;
    border: none;
    background-color: #4E90D9;
    border-radius: none;
    box-shadow: none;

    @media(max-width:1919px) {
        width: 24px;
        height: 24px;
    }

    @media(max-width:767px) {
        width: 16px;
        height: 16px;
    }

    &.state_hover {
        background-color: #4E90D9 !important;
    }

    &:hover {
        background-color: #4E90D9 !important;
    }
}

.irs-single {
    display: none;
}

.irs-min {
    background-color: transparent !important;

    top: unset !important;
    bottom: calc(100% + 6px);
    left: -10px !important;
    visibility: visible !important;

    font-family: Open Sans !important;
    font-style: normal !important;
    font-weight: normal !important;
    font-size: 14px !important;
    line-height: 19px !important;
    text-align: right !important;
    color: #8A8A8A !important;
}

.irs-max {
    background-color: transparent !important;

    top: unset !important;
    bottom: calc(100% + 6px);
    right: -10px !important;
    visibility: visible !important;

    font-family: Open Sans !important;
    font-style: normal !important;
    font-weight: normal !important;
    font-size: 14px !important;
    line-height: 19px !important;
    text-align: left !important;
    color: #8A8A8A !important;
}