.contacts__intro {
    @media(max-width:767px) {
        margin-bottom: 40px;
    }
}

.contacts__content-inner {
    position: relative;
}

.contacts__info {
    max-width: 433px;

    font-weight: bold;
    font-size: 18px;
    line-height: 25px;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 16px;
        line-height: 21px;
    }

    @media(max-width:1023px) {
        font-size: 14px;
        line-height: 19px;
    }


    span {
        font-size: 18px;
        line-height: 25px;
        color: #545454;
        font-weight: 400;

        @media(max-width:1919px) {
            font-size: 16px;
            line-height: 21px;
        }

        @media(max-width:1023px) {
            font-size: 14px;
            line-height: 19px;
        }
    }

    &+& {
        margin-top: 30px;

        @media(max-width:1023px) {
            margin-top: 20px;
        }

        @media(max-width:767px) {
            margin-top: 15px;
        }
    }

    &--link {
        display: flex;
        align-items: center;

        span {
            display: block;
            margin-right: 5px;
        }
    }
}

.contacts__link {
    font-size: 18px;
    line-height: 25px;
    color: #545454;
    font-weight: 400;

    display: inline-block;
    margin-left: 5px;
    overflow-x: hidden;
    position: relative;

    @media(max-width:1919px) {
        font-size: 16px;
        line-height: 21px;
    }

    @media(max-width:1023px) {
        font-size: 14px;
        line-height: 19px;
    }

    &::before {
        content: '';
        height: 1px;
        background-color: #545454;

        transform: translateX(-100%);
        transition: .2s linear;

        position: absolute;
        left: 0;
        right: 0;
        bottom: 3px;
    }

    @media(min-width:1024px) {
        &:hover::before {
            transform: translateX(0%);
        }
    }
}

.telegram__link {
    display: block;
    width: 40px;
    height: 40px;
    margin-left: 20px;

    transition: .2s linear;

    background-image: url('../img/social/telegram.svg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;

    @media(min-width:1024px) {
        &:hover {
            transform: scale(1.1);
        }
    }

    @media(max-width:1023px) {
        margin-left: 11px;
    }

    @media(max-width:767px) {
        width: 22px;
        height: 22px;
    }
}

.contacts__photo {
    width: 774px;
    height: 518px;

    position: absolute;
    top: -130px;
    right: -177px;

    @media(max-width:1919px) {
        width: 684px;
        height: 448px;
    }

    @media(max-width:1599px) {
        width: 624px;
        height: 409px;
        top: -40px;
        right: -27px;
    }

    @media(max-width:1439px) {
        width: 471px;
        height: 340px;
        top: 10px;
        right: 15px;
    }

    @media(max-width:1023px) {
        display: none;
    }
}

.contacts__btn {
    width: 208px;
    margin-top: 20px;

    @media(max-width:767px) {
        margin-top: 15px;
    }
}

.contacts__content {
    @media(max-width:767px) {
        margin-bottom: 30px;
    }
}