.services--ceo {
    .order__inner {
        margin-bottom: 200px;

        @media(max-width:1919px) {
            margin-bottom: 120px;
        }

        @media(max-width:1439px) {
            margin-bottom: 60px;
        }

        @media(max-width:1023px) {
            margin-bottom: 40px;
        }
    }
}

.services__intro {
    @media(max-width:767px) {
        margin-bottom: 40px;
    }
}

.services__intro-inner {
    position: relative;
}

.services__subtitle {
    max-width: 476px;
}

.services__photo {
    width: 451px;
    height: 428px;

    position: absolute;
    top: -91px;
    right: -55px;

    img {
        object-fit: contain;
    }

    @media(max-width:1919px) {
        width: 311px;
        height: 298px;

        right: 0px;
    }

    @media(max-width:1439px) {
        width: 241px;
        height: 258px;
    }

    @media(max-width:1023px) {
        display: none;
    }
}

.services__content {
    @media(max-width:1919px) {
        max-width: 1000px;
        margin: 0 auto;
    }
}

.services__wrapper {
    @media(max-width:1023px) {
        position: relative;
        margin-bottom: 20px;

        &::before {
            content: '';
            width: 9px;
            height: 11px;

            background-image: url("../img/triangle.svg");
            background-repeat: no-repeat;
            background-size: 100% 100%;
            pointer-events: none;

            position: absolute;
            right: 15px;
            top: 58px;
            transition: .3s linear;
        }

        &.active::before {
            transform: rotate(180deg);
        }
    }

    @media(max-width:767px) {
        &::before {
            width: 6px;
            height: 8px;
            top: 50px;
        }
    }
}

.services__list {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 10px;
    margin-bottom: 60px;

    @media(max-width:1919px) {
        margin-bottom: 30px;
    }

    @media(max-width:1023px) {
        display: flex;
        flex-direction: column;
        padding: 0;
        height: auto;
        grid-gap: 0;
        max-height: 0;

        background-color: #F8FAFD;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 10px;

        position: absolute;
        top: calc(100% + 4px);
        left: 0;
        right: 0;
        z-index: 10;

        transition: .3s linear;

        &.active {
            max-height: 187px;
            padding: 7.5px 0;
            z-index: 20;
        }

        overflow: scroll;

        &::-webkit-scrollbar {
            display: none;
        }

        -ms-overflow-style: none;
        scrollbar-width: none;
    }
}

.services__item {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 66px;

    transition: .2s linear;

    cursor: pointer;
    background-color: #F8FAFD;
    border-radius: 10px;

    &.active {
        background-color: #EBF0F8;
    }

    @media(min-width:1024px) {
        &:hover:not(.active) {
            opacity: .7;
        }
    }

    @media(max-width:1919px) {
        height: 50px;
    }

    @media(max-width:1023px) {
        height: 42px;
        min-height: 42px;
        width: 100%;
        justify-content: flex-start;
        padding: 0 15px;
        border-radius: 0;
    }
}

.services__img {
    margin-right: 10px;

    img {
        width: unset;
        height: unset;
    }

    @media(max-width:1919px) {
        max-width: 20px;

        img {
            width: 100%;
            height: auto;
        }
    }

    @media(max-width:1023px) {
        display: none;
    }
}

.services__name {
    font-weight: 600;
    font-size: 18px;
    line-height: 25px;
    text-align: center;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 16px;
        line-height: 23px;
    }

    @media(max-width:1023px) {
        font-weight: normal;
        font-size: 14px;
        line-height: 16px;
        color: #545454;
        text-transform: capitalize;
    }
}

.rates {
    margin-bottom: 80px;

    &--purchase-history {
        margin-right: -15px;
        margin-bottom: 0;
        max-height: 794px;

        overflow: hidden;
        @include list-scroll;

        @media(max-width:1023px) {
            margin-right: 0;
            max-height: 477px;
        }

        .rates__top {
            @media(max-width:1023px) {
                padding-bottom: 33px;
                position: relative;
            }

            @media(max-width:767px) {
                padding-bottom: 24px;
                margin-bottom: 0;
            }
        }

        .rates__item {
            padding: 20px 20px 20px 28px;
            margin-right: 15px;

            background: #FFFFFF;

            @media(max-width:1919px) {
                padding: 15px 10px;
            }

            @media(max-width:767px) {
                padding: 10px;
                margin-right: 7px;
            }
        }

        .rates__logo {
            width: 56px;
            height: 56px;



            @media(max-width:1919px) {
                width: 43px;
                height: 43px;
                margin-right: 15px;
            }

            @media(max-width:1439px) {
                width: 38px;
                height: 38px;
            }

            @media(max-width:767px) {
                width: 33px;
                height: 33px;
                margin-right: 10px;
            }
        }

        .rates__title {
            font-size: 18px;
            line-height: 25px;

            @media(max-width:1919px) {
                font-size: 16px;
                line-height: 23px;
            }

            @media(max-width:1439px) {
                position: static;
                font-size: 14px;
                line-height: 16px;
            }
        }

        .rates__price {
            margin-left: 30px;
        }

        .rates__date,
        .rates__number,
        .rates__price {
            font-size: 18px;
            line-height: 25px;

            @media(max-width:1919px) {
                font-size: 16px;
                line-height: 23px;
            }

            
            @media(max-width:1439px) {
                font-size: 14px;
                line-height: 16px;
            }

            @media(max-width:1023px) {
                position: absolute;
                bottom: 0;
            }

            @media(max-width:767px) {
                font-size: 10px;
                line-height: 14px;
                margin-left: 0;
            }
        }

        .rates__date {
            @media(max-width:1023px) {
                left: 4px;
            }

        }

        .rates__number {
            @media(max-width:1023px) {
                left: 50%;
                transform: translateX(-50%);
            }
        }

        .rates__price {
            @media(max-width:1023px) {
                right: 4px;
            }

            @media(max-width:767px) {
                left: unset;
            }
        }

        .rates__value {
            margin-left: 10px;
            font-size: 16px;
            line-height: 22px;

            @media(max-width:1919px) {
                font-size: 14px;
                line-height: 18px;
            }

            @media(max-width:767px) {
                font-size: 10px;
                line-height: 14px;
                margin-left: 5px;
            }
        }

        .rates__bottom {
            @media(max-width:767px) {
                margin-top: 10px;
            }
        }
    }
}

.rates__item {
    max-width: 1150px;
    padding: 10px 20px 10px 24px;
    margin-right: 10px;

    background-color: #F8FAFD;
    border-radius: 10px;

    &+& {
        margin-top: 20px;

        @media(max-width:1023px) {
            margin-top: 10px;
        }
    }

    &--services {
        max-width: unset;
        margin-right: 0;
    }

    @media(max-width:767px) {
        padding: 15px 10px 10px;
    }
}

.rates__top {
    display: flex;
    align-items: center;

    @media(max-width:767px) {
        margin-bottom: 5px;
        position: relative;
    }
}

.rates__logo {
    width: 42px;
    height: 42px;
    margin-right: 20px;

    @media(max-width:1919px) {
        width: 38px;
        height: 38px;
    }

    @media(max-width:1439px) {
        width: 35px;
        height: 35px;
    }

    @media(max-width:767px) {
        width: 33px;
        height: 33px;
        margin-right: 10px;
    }
}

.rates__title {
    font-weight: bold;
    font-size: 22px;
    line-height: 30px;
    text-align: center;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 18px;
        line-height: 26px;
    }

    @media(max-width:1439px) {
        font-size: 16px;
        line-height: 18px;
    }

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 16px;

        position: absolute;
        top: 0;
        left: 43px;
    }
}

.rates__price,
.rates__date,
.rates__number {
    margin-left: auto;
    font-weight: bold;
    font-size: 22px;
    line-height: 30px;
    text-align: center;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 18px;
        line-height: 26px;
    }

    @media(max-width:1439px) {
        font-size: 16px;
        line-height: 18px;
    }

    @media(max-width:767px) {
        font-size: 14px;
    }
}

.rates__number {
    margin-left: 30px;
}

.rates__price {
    @media(max-width:767px) {
        position: absolute;
        left: 41px;
        bottom: 0;
    }
}

.rates__value {
    display: inline-block;
    margin-left: 15px;

    font-weight: 600;
    font-size: 18px;
    line-height: 25px;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 21px;
        margin-left: 10px;
    }

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 16px;
    }
}

.rates__show-more {
    margin: 0 15px 0 27px;

    @media(max-width:1023px) {
        display: none;
    }

    span {
        font-size: inherit;
        line-height: inherit;

        &:nth-child(1) {
            display: inline;
        }

        &:nth-child(2) {
            display: none;
        }
    }

    &.active {
        span {
            &:nth-child(1) {
                display: none;
            }

            &:nth-child(2) {
                display: inline;
            }
        }
    }
}

.rates__btn-order {
    @media(max-width:1023px) {
        margin-left: 20px;
        width: 37px;
        height: 37px;

        background-color: transparent;
        color: transparent;
        background-image: url('../img/rates-btn.svg');
    }

    @media(max-width:767px) {
        margin-left: auto;
    }
}

.rates__bottom {
    margin-top: 6px;
    padding: 0 62px 10px;
    display: none;

    @media(max-width:767px) {
        padding: 0;
    }
}

.rates__descr {
    max-width: 688px;
    margin-bottom: 30px;

    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 25px;
    color: #545454;


    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 21px;
        margin-left: 10px;
    }

    @media(max-width:767px) {
        max-width: 290px;
        margin-left: 0;
        margin-bottom: 15px;

        font-size: 14px;
        line-height: 16px;
    }
}

.rates__info {
    display: flex;
    align-items: center;

    @media(max-width:767px) {
        flex-direction: column;
        align-items: flex-start;
    }
}

.rates__info-item {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 21px;
    }

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 16px;
    }

    &+& {
        margin-left: 60px;

        @media(max-width:767px) {
            margin-left: 0;
            margin-top: 5px;
        }
    }
}

.services__info {
    display: flex;
    justify-content: space-between;

    @media(max-width:1023px) {
        flex-direction: column;
    }
}

.services__info-item {
    width: calc(50% - 15px);

    @media(max-width:1023px) {
        width: 100%;
    }

    &+& {
        padding-top: 44px;

        @media(max-width:1023px) {
            padding-top: 0;
        }
    }

    p {
        margin-bottom: 20px;

        font-weight: normal;
        font-size: 16px;
        line-height: 22px;
        color: #545454;

        @media(max-width:1919px) {
            font-size: 14px;
            line-height: 16px;

            margin-bottom: 15px;
        }
    }

    b {
        display: inline-block;
        margin-bottom: 20px;

        font-weight: 600;
        font-size: 16px;
        line-height: 22px;
        color: #545454;

        @media(max-width:1919px) {
            font-size: 14px;
            line-height: 16px;

            margin-bottom: 15px;
        }
    }

    ul {
        padding-left: 5px;
        margin-bottom: 20px;

        @media(max-width:767px) {
            margin-bottom: 15px;
        }
    }

    li {
        list-style: inside;

        font-weight: normal;
        font-size: 16px;
        line-height: 28px;
        color: #545454;

        @media(max-width:1919px) {
            font-size: 14px;
            line-height: 24px;
        }

        &+& {
            margin-top: 5px;

            @media(max-width:767px) {
                margin-top: 0;
            }
        }
    }

}

.order-again__btn {
    margin-left: 40px;

    @media(max-width:1919px) {
        margin-left: 20px;
    }

    @media(max-width:1023px) {
        width: 37px;
        height: 37px;
        margin-left: auto;
        background: url("../img/rates-btn.svg");
        background-size: cover;
        background-repeat: no-repeat;
        color: transparent;
    }
}