.fast-payment {
    display: none;
    background: #F8FAFD;
    border-radius: 10px;

    padding: 30px;

    position: relative;

    &.show {
        display: block;

        animation: fade .7s linear;
    }

    @media(max-width:1919px) {
        padding: 15px;
    }

    @media(max-width:767px) {
        padding: 20px 18px;
    }

    .payment-method__list {
        margin-bottom: 30px;

        @media(max-width:1919px) {
            margin-left: 10px;
        }

        @media(max-width:1100px) {
            justify-content: space-between;
        }

        @media(max-width:950px) {
            flex-direction: column;
            align-items: flex-start;
        }
    }

    .payment-method__content {
        @media(max-width:950px) {
            &::before {
                height: 55px;
                width: 298px;
                right: unset;
            }
        }
    }

    @media(max-width:1919px) {
        .payment-method__icon {
            max-width: 40px;

            img {
                width: 100%;
                height: auto;
                object-fit: contain;
            }
        }

        .payment-method__name {
            font-size: 14px;
            line-height: 19px;
            margin-bottom: 7px;
        }

        .payment-method__commission {
            font-size: 14px;
            line-height: 14px;
        }
    }

    .payment-method__item+.payment-method__item {
        margin-left: 88px;

        @media(max-width:1100px) {
            margin-left: 0;
        }

        @media(max-width:950px) {
            margin-top: 15px;
        }
    }

    @media(max-width:1919px) {
        .payment-method__content::before {
            height: 60px;
        }
    }
}

.fast-payment__btn-back {
    position: absolute;
    top: 32px;
    left: 25px;

    @media(max-width:1919px) {
        top: 15px;
        left: 20px;
    }

    @media(max-width:767px) {
        border: none;
        color: transparent;

        width: 36px;
        height: 36px;
        background-image: url("../img/btn.svg");
    }
}

.fast-payment__title {
    margin-bottom: 48px;

    font-weight: 600;
    font-size: 30px;
    line-height: 41px;
    text-align: center;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 24px;
        line-height: 34px;
        margin-bottom: 38px;
    }

    @media(max-width:767px) {
        font-size: 20px;
        line-height: 27px;
        margin-bottom: 22px;
    }
}


.fast-payment__block {
    display: flex;
    align-items: center;
    margin-bottom: 61px;

    @media(max-width:1919px) {
        margin-bottom: 40px;
    }

    @media(max-width:767px) {
        flex-direction: column-reverse;
        margin-bottom: 30px;
    }
}

.fast-payment__btn-pay {
    margin-right: 40px;
    width: auto;
    padding: 0 10px;

    @media(max-width:767px) {
        margin-top: 20px;
        margin-right: 0;
    }
}

.fast-payment__group {
    display: flex;
    align-items: center;

    &.error {
        .fast-payment__input {
            border: 1px solid #FF4848;
        }

        .fast-payment__error-text {
            display: block;
        }
    }

    @media(max-width:767px) {
        display: block;
        position: relative;
    }
}

.fast-payment__input-name {
    display: none;
    margin-bottom: 15px;

    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    color: #545454;

    @media(max-width:767px) {
        display: block;
    }
}

.fast-payment__input {
    width: 278px;
    height: 47px;
    margin-right: 15px;

    @media(max-width:1023px) {
        height: 39px;
    }

    @media(max-width:767px) {
        width: 288px;
        margin-right: 0;
    }
}

.fast-payment__error-text {
    display: none;

    font-weight: normal;
    font-size: 18px;
    line-height: 25px;
    color: #FF4848;
    white-space: nowrap;


    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 14px;

        position: absolute;
        top: 0;
        right: 0;
    }
}

.fast-payment__data-title {
    margin-bottom: 40px;

    font-weight: 600;
    font-size: 24px;
    line-height: 33px;
    text-align: center;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 18px;
        line-height: 22px;
        margin-bottom: 30px;
    }

    @media(max-width:1439px) {
        margin-bottom: 20px;
    }

    @media(max-width:767px) {
        font-size: 16px;
        line-height: 22px;
    }
}

.fast-payment__data-info {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}

.fast-payment__data-item {
    margin-bottom: 30px;

    @media(max-width:1919px) {
        margin-bottom: 20px;
    }

    @media(max-width:767px) {
        margin-bottom: 20px;
    }

    &:nth-child(2) {
        margin-left: 30px;
        margin-right: 30px;

        @media(max-width:1919px) {
            margin-left: 20px;
            margin-right: 20px;
        }

        @media(max-width:767px) {
            margin-left: 24px;
            margin-right: 24px;
        }

        @media(max-width:500px) {
            margin-right: 0;
        }
    }

    &:nth-child(3) {
        .fast-payment__data-field {
            width: 571px;

            @media(max-width:767px) {
                text-align: start;
                padding: 10px 20px;
                width: 100%;
                min-width: 288px;
            }
        }
    }

    &:nth-child(4) {
        width: 100%;
        margin-bottom: 0;
        padding: 20px 15px 35px;

        background: #FFFFFF;
        border-radius: 10px;

        @media(max-width:1919px) {
            padding: 15px 15px 25px;
        }

        @media(max-width:767px) {
            padding: 10px 10px 20px;
        }

        .fast-payment__data-name {
            font-weight: bold;
            font-size: 22px;
            line-height: 30px;
            color: #545454;

            margin-bottom: 19px;

            @media(max-width:1919px) {
                font-size: 16px;
                line-height: 22px;
                margin-bottom: 15px;
            }

            @media(max-width:767px) {
                font-size: 14px;
                line-height: 19px;
                margin-bottom: 10px;
            }
        }
    }
}

.fast-payment__data-name {
    margin-bottom: 15px;

    font-weight: bold;
    font-size: 18px;
    line-height: 25px;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 16px;
    }
}

.fast-payment__data-field {
    padding: 16px 43px;

    background: #FFFFFF;
    border-radius: 10px;

    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 25px;
    color: #545454;

    @media(max-width:1919px) {
        padding: 14px 38px;
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:767px) {
        padding: 10px;
        min-width: 132px;
        text-align: center;
    }
}

.fast-payment__data-descr {
    max-width: 835px;

    font-weight: normal;
    font-size: 18px;
    line-height: 25px;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
    }
}