.reviews-main {
    margin-bottom: 120px;

    @media(max-width:1919px) {
        margin-bottom: 90px;

        .rating__item {
            fill: #F5AD4A;

            &:nth-child(1n + 2) {
                display: none;
            }
        }

        .rating__total {
            display: flex;
        }
    }

    @media(max-width:1599px) {
        margin-bottom: 60px;
    }

    @media(max-width:767px) {
        margin-bottom: 40px;
    }
}

.reviews-main__title {
    margin-bottom: 60px;

    @media(max-width:1599px) {
        margin-bottom: 40px;
    }

    @media(max-width:1023px) {
        margin-bottom: 30px;
    }
}

.reviews-main__list {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px;
    margin-bottom: 30px;

    @media(max-width:1023px) {
        overflow-x: scroll;
        grid-gap: 12px;
        margin-bottom: 15px;
    }
}

.reviews-main__item {
    height: 231px;
    padding: 10px;

    background-color: #F8FAFD;
    border-radius: 10px;

    @media(max-width:1919px) {
        height: auto;
    }

    @media(max-width:1023px) {
        min-width: 295px;
    }
}

.reviews-main__info {
    display: flex;
    margin-bottom: 20px;
}

.reviews-main__avatar {
    min-width: 54px;
    height: 54px;
    margin-right: 15px;

    border-radius: 50%;
    overflow: hidden;

    @media(max-width:1023px) {
        min-width: 36px;
        width: 36px;
        height: 36px;
        margin-right: 10px;
    }
}

.reviews-main__details {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}

.reviews-main__name {
    font-weight: bold;
    font-size: 18px;
    line-height: 25px;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 16px;
        line-height: 22px;
    }

    @media(max-width:1023px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:767px) {
        line-height: 16px;
    }
}


.reviews-main__date {
    width: 100%;
    margin-top: 10px;
    font-weight: normal;

    font-size: 14px;
    line-height: 19px;
    color: #696969;

    @media(max-width:1919px) {
        line-height: 16px;
        margin-top: 4px;
    }

    @media(max-width:767px) {
        line-height: 14px;
    }
}

.reviews-main__descr {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #696969;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:767px) {
        line-height: 16px;
    }
}

.reviews-main__btn {
    margin: 0 auto;
}

.reviews__intro {
    @media(max-width:767px) {
        background-size: inherit;
        background-position: inherit;
        margin-bottom: 40px;
    }

    @media(max-width:375px) {
        background-size: cover;
        background-position: center;
    }
}

.reviews__intro-inner {
    display: flex;

    position: relative;

    @media(max-width:767px) {
        flex-direction: column;
    }
}

.reviews__intro-content {
    margin-right: 100px;

    @media(max-width:1919px) {
        margin-right: 50px;
    }

    @media(max-width:767px) {
        margin-right: 0;
        margin-bottom: 15px;
    }
}

.reviews__subtitle {
    max-width: 390px;
}

.reviews__regulations-title {
    margin-top: 14px;
    margin-bottom: 10px;

    font-weight: bold;
    font-size: 18px;
    line-height: 25px;
    color: #FFFFFF;

    @media(max-width:1919px) {
        margin-top: 5px;
        font-size: 16px;
        line-height: 22px;
    }

    @media(max-width:1439px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 16px;
        margin-top: 0;
        margin-bottom: 8px;
    }
}

.reviews__regulations-list {
    padding-left: 10px;
}


.reviews__regulations-item {
    list-style: inside;

    font-weight: normal;
    font-size: 18px;
    line-height: 150.68%;
    color: #FFFFFF;

    @media(max-width:1919px) {
        font-size: 16px;
        line-height: 23px;
    }

    @media(max-width:1439px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:767px) {
        font-size: 14px;
        line-height: 150.68%;
    }
}

.reviews__photo {
    width: 501px;
    height: 501px;

    position: absolute;
    right: -86px;
    top: -142px;

    @media(max-width:1919px) {
        display: none;
    }
}

.reviews__content {
    @media(max-width:1919px) {
        max-width: 1000px;
        margin: 0 auto;
    }
}

.reviews__title {
    margin-bottom: 60px;

    font-weight: 600;
    font-size: 30px;
    line-height: 41px;
    color: #545454;
    text-align: center;

    @media(max-width:1919px) {
        font-size: 25px;
        line-height: 36px;
        margin-bottom: 30px;
    }

    @media(max-width:1439px) {
        font-size: 20px;
        line-height: 27px;
    }
}

.reviews__messenger {
    background-color: #F8FAFD;
    border-radius: 10px;
    padding: 20px;
    margin-bottom: 120px;

    @media(max-width:1919px) {
        margin-bottom: 60px;
        padding: 15px;
    }

    @media(max-width:767px) {
        margin-bottom: 40px;
        padding: 15px 10px;
    }
}

.reviews__content {
    @media(max-width:767px) {
        margin-bottom: 30px;
    }
}

.reviews__textarea {
    display: block;
    width: 100%;
    min-height: 164px;
    padding: 15px;
    margin-bottom: 20px;

    background-color: #FFFFFF;
    border-radius: 10px;
    border: none;
    resize: none;

    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #545454;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 28px;
    }

    @media(max-width:767px) {
        padding: 10px;
        margin-bottom: 15px;

        line-height: 16px;
    }

    &::placeholder {
        font-family: Open Sans;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 22px;
        color: #D5D5D5;


        @media(max-width:1919px) {
            font-size: 14px;
            line-height: 28px;
        }

        @media(max-width:767px) {
            line-height: 16px;
        }
    }
}

.reviews__nav {
    display: flex;
    align-items: center;
}


.reviews__authorization {
    display: flex;
    align-items: center;
    margin-left: 30px;

    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #FF6565;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 28px;
    }

    @media(max-width:767px) {
        line-height: 16px;
        margin-left: 18px;
        display: block;
    }
}

.reviews__authorization-btn {
    margin-right: 5px;

    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #FF6565;

    overflow: hidden;
    position: relative;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 28px;
    }

    @media(max-width:767px) {
        line-height: 16px;
        margin-right: 0;
    }

    &::before {
        content: '';
        height: 1px;
        background-color: #FF6565;
        transition: .2s linear;

        position: absolute;
        left: 0;
        right: 0;
        bottom: 2px;
    }

    @media(min-width:1024px) {
        &:hover::before {
            transform: translateX(100%);
        }
    }
}

.reviews__list {
    margin-bottom: 40px;

    @media(max-width:767px) {
        margin-bottom: 20px;
    }
}

.reviews__item {
    background: #F8FAFD;
    border-radius: 10px;
    padding: 20px;

    @media(max-width:1919px) {
        padding: 15px;
    }

    @media(max-width:1023px) {
        padding: 15px;
    }

    @media(max-width:767px) {
        padding: 10px;
    }

    &+& {
        margin-top: 20px;

        @media(max-width:1023px) {
            margin-top: 15px;
        }

        @media(max-width:767px) {
            margin-top: 10px;
        }
    }
}

.reviews__descr {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #696969;

    @media(max-width:1919px) {
        font-size: 14px;
        line-height: 18px;
    }

    @media(max-width:767px) {
        line-height: 16px;
    }
}