.faq__intro {
    @media(max-width:767px) {
        margin-bottom: 40px;
    }
}

.faq__intro-inner {
    position: relative;
}

.faq__subtitle {
    display: flex;
    flex-wrap: wrap;
    max-width: 473px;

    @media(max-width:1023px) {
        display: block;
        max-width: 363px;
    }
}

.faq__link {
    display: inline-block;
    margin-left: 5px;

    font-weight: 400;
    font-size: 18px;
    line-height: 25px;

    overflow-x: hidden;
    position: relative;

    &::before {
        content: '';
        height: 1px;
        background-color: #fff;

        transition: .2s linear;

        position: absolute;
        left: 0;
        right: 0;
        bottom: 3px;

        @media(max-width:1023px) {
            display: none;
        }
    }

    @media(min-width:1024px) {
        &:hover::before {
            transform: translateX(100%);
        }
    }

    @media(max-width:1919px) {
        font-size: 16px;
        line-height: 23px;
    }

    @media(max-width:1599px) {
        font-size: 14px;
        line-height: 20px;
    }

    @media(max-width:1023px) {
        display: inline;
        margin-left: 0;
        text-decoration: underline !important;
    }
}

.faq__photo {
    width: 719px;
    height: 392px;

    position: absolute;
    top: -74px;
    right: -195px;

    @media(max-width:1919px) {
        width: 589px;
        height: 313px;
        top: -24px;
        right: -88px;

        img {
            object-fit: contain;
        }
    }

    @media(max-width:1599px) {
        width: 529px;
        height: 253px;
        top: -24px;
        right: -8px;
    }

    @media(max-width:1439px) {
        width: 489px;
        height: 203px;
        top: -44px;
        right: -8px;
    }

    @media(max-width:1023px) {
        display: none;
    }
}

.faq__content {
    margin-bottom: 60px;

    @media(max-width:767px) {
        margin-bottom: 30px;
    }
}